import moment from 'moment';
import React from 'react';
import { useState } from 'react';
import DateTimePicker from 'react-datetime-picker';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import Swal from 'sweetalert2';
import { startCargarEvento } from '../../actions/eventos';
import { useForm } from '../../hooks/useForm';
import { QRPuerta } from '../ui/QRPuerta';
import { PDFDownloadLink } from '@react-pdf/renderer';
import { Comprobante } from '../ui/Comprobante';
const now = moment();

export const AddEvento = () => {
    const qrCodeCanvas = document.querySelector('canvas');
    const dataUrl = qrCodeCanvas&&qrCodeCanvas.toDataURL('image/png', 0.3);
    const { uid } = useSelector((state) => state.auth);
    const [date, setDate] = useState(now.toDate());
    const dispatch = useDispatch();
    const history = useHistory();
    const [formEventoValues, handleEventoInputChange] = useForm({
        eNombre:"",
        eUbicacion: "",
        eVip:"",
        ePreferencial:"", 
        eGeneral:"", 
        ePuerta:""
    });
    const { eNombre,eUbicacion, eVip, ePreferencial, eGeneral, ePuerta } = formEventoValues;
    const handleAddEvento = (e) => {
        e.preventDefault();
        if (eUbicacion === "" || eNombre === "") {
          return Swal.fire(
            "Error",
            "Debes de rellenar todos los campos",
            "error"
          );
        }
        dispatch(startCargarEvento(eNombre, eUbicacion, eVip, ePreferencial, eGeneral, ePuerta, date, uid));
    };
    const handleVolver = () => {
        history.push('/controlevento');
    };
    const handleDateChange = (e) => {
        setDate(e);
    };
  return ( 
  <div className="col-md-6 login-form-2">
  <h3>Cargar Evento</h3>
  <form onSubmit={handleAddEvento}>
    
    <div className="form-group">
      <input
        type="text"
        className="form-control mb-2 text-center"
        placeholder="Nombre"
        name="eNombre"
        value={eNombre}
        onChange={handleEventoInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className="form-control mb-2 text-center"
        placeholder="Ubicacion"
        name="eUbicacion"
        value={eUbicacion}
        onChange={handleEventoInputChange}
      />
    </div>
    <div className="form-group">
            <DateTimePicker
              onChange={handleDateChange}
              value={date}
              className="form-control mb-2 text-center"
              format='dd-MM-yyyy'
            />
    </div>
    <div className="form-group d-flex justify-content-center" style={{color:"white"}}>
      <label className='h5'>Precios por entrada anticipada</label>
    </div>
    <div className="form-group">
      <input
        type="number"
        className="form-control mb-2 text-center"
        placeholder="Vip"
        name="eVip"
        value={eVip}
        onChange={handleEventoInputChange}
      />
    </div>

    <div className="form-group">
      <input
        type="number"
        className="form-control mb-2 text-center"
        placeholder="Preferencial"
        name="ePreferencial"
        value={ePreferencial}
        onChange={handleEventoInputChange}
      />
    </div>

    <div className="form-group">
      <input
        type="number"
        className="form-control mb-3 text-center"
        placeholder="General"
        name="eGeneral"
        value={eGeneral}
        onChange={handleEventoInputChange}
      />
    </div>
    <div className="form-group d-flex justify-content-center" style={{color:"white"}}>
      <label className='h5'>Precio por entrada por puerta</label>
    </div>
    <div className="form-group">
      <input
        type="number"
        className="form-control mb-3 text-center"
        placeholder="Precio"
        name="ePuerta"
        value={ePuerta}
        onChange={handleEventoInputChange}
      />
    </div>

    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Cargar" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
  <QRPuerta evento={eNombre} dataUrl={dataUrl} />
  {dataUrl&&<PDFDownloadLink
        document={ <Comprobante datos={{uri:dataUrl,evento:eNombre}}/>}
        fileName={`qr-${eNombre}.pdf`}
        className="text-decoration-none downloadpdfwsp"
      >
        <div className="form-group d-flex justify-content-center">
          <button className="btnSubmit mt-2">Descargar QR</button>
        </div>
  </PDFDownloadLink>}
</div>);
};
