import React, { useState } from 'react';
import DateTimePicker from 'react-datetime-picker';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { startActualizarEvento } from '../../actions/eventos';
import { useForm } from '../../hooks/useForm';

export const EventoEditar = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    const { activeEvent } = useSelector((state) => state.eventos);
    const [date, setDate] = useState(activeEvent.fecha);
    const [formEventoValues, handleEventoInputChange] = useForm({
        eUbicacion: activeEvent ? activeEvent.ubicacion:"",
        eVip: activeEvent ? activeEvent.vip:"",
        ePreferencial:activeEvent ? activeEvent.preferencial:"",
        eGeneral:activeEvent ? activeEvent.general:"",
        ePuerta:activeEvent ? activeEvent.puerta:"",
    });
    const { eUbicacion, eVip, ePreferencial, eGeneral,ePuerta } = formEventoValues;
    const handleDateChange = (e) => {
        setDate(e);
    };
    const handleUpdateEvento = (e) => {
        e.preventDefault();
        dispatch(startActualizarEvento(activeEvent.id, eUbicacion, eVip, ePreferencial, eGeneral,ePuerta, date));
    };
    const handleVolver = (e) => {
        history.push('/controlevento');
    };
  return (
  <div>
    
    <div className='container'>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
            <label className='form-label h3'><b>Nombre del evento</b></label>
            {activeEvent && <h3 className='text-dark'>{activeEvent.nombre}</h3>}
        </div>
        <form onSubmit={handleUpdateEvento}>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>Ubicacion</b></label>
            <div><input className='border rounded-pill text-center form-control' name="eUbicacion" type="text" value={eUbicacion} onChange={handleEventoInputChange}/></div>
        </div>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>Fecha</b></label>
            <DateTimePicker
              onChange={handleDateChange}
              value={date}
              className="border rounded-pill text-center form-control"
              format='dd-MM-yyyy'
            />
        </div>
        <div className='form-group'>
            <h4>Precios de las entradas</h4>
        </div>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>VIP</b></label>
            <div><input className='border rounded-pill text-center form-control' name="eVip" type="number" value={eVip} onChange={handleEventoInputChange}/></div>
        </div>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>Preferencial</b></label>
            <div><input className='border rounded-pill text-center form-control' name="ePreferencial" type="number" value={ePreferencial} onChange={handleEventoInputChange}/></div>
        </div>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-3'>
        <label className='form-label h5'><b>General</b></label>
            <div><input className='border rounded-pill text-center form-control' name="eGeneral" type="number" value={eGeneral} onChange={handleEventoInputChange}/></div>
        </div>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-3'>
        <label className='form-label h5'><b>Puerta</b></label>
            <div><input className='border rounded-pill text-center form-control' name="ePuerta" type="number" value={ePuerta} onChange={handleEventoInputChange}/></div>
        </div>
        <div className="form-group d-flex justify-content-center mb-2">
            <button type="submit" className="btn btn-success" ><i className="fas fa-check"></i> Actualizar</button>
        </div>
        </form>
        <div className="form-group d-flex justify-content-center">
    <button className="btn btn-primary mb-5" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
  </div>
    </div>
  </div>
  );
};
