import React from 'react';
import moment from 'moment';
import { useDispatch, useSelector } from 'react-redux';
import { startAsociarCliente, startDesasociarClienteSinEvento, startSetActive } from '../../actions/clientes';
import { uiOpenModal } from '../../actions/ui';
import {isMobile} from 'react-device-detect';
import { useHistory } from 'react-router-dom';

export const ClienteItems = ({clientes,loading}) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { uid } = useSelector((state) => state.auth);
    if(loading){
        return <h2>Cargando...</h2>
    }
    // const handleGoAsociar = (uid) => {
    //     dispatch(startAsociarCliente(uid));
    //     dispatch(uiOpenModal());
    // };
    const handleGoDesasociar = (cid) => {
      dispatch(startDesasociarClienteSinEvento(cid,uid));
      setTimeout(()=>{
        history.push(`/`);
      },500);
  };
    const handleGoEditar = (uid) => {
      dispatch(startSetActive(uid));
      setTimeout(()=>{
        history.push(`/editarcliente/${uid}`);
      },500);
    };
  return (
    <>
      {clientes && <table className={isMobile ? "d-block overflow-scroll table table-bordered table-striped":"table table-bordered table-striped"}>
    <thead className="bg-dark text-light">
      <tr>
        <th className="text-center align-middle" scope="col">DNI</th>
        <th className="text-center align-middle" scope="col">Nombre</th>
        <th className="text-center align-middle" scope="col">Apellido</th>
        <th className="text-center align-middle" scope="col">Celular</th>
        <th className="text-center align-middle" scope="col">Eventos</th>
        <th className="text-center align-middle" scope="col">Usuario</th>
        {/* <th className="text-center align-middle" scope="col">Entradas</th
        > */}
        <th className="text-center align-middle" scope="col">Editar</th>
        <th className="text-center align-middle" scope="col">Eliminar</th>
      </tr>
    </thead>
    <tbody>
    {clientes.map(item => {
        return (
          <tr key={item.uid}>
            <td className="text-center">{ item.dni }</td>
            <td className="text-center">{ item.nombre }</td>
            <td className="text-center">{ item.apellido }</td>
            <td className="text-center">{ item.celular }</td>
            <td className="text-center">
            {item.entradas.map(entrada => {
                return <pre key={item.uid}>{entrada.nombre} - ${entrada.importe} - {entrada.tipo}</pre>
            })}
            </td>
            {/* {item.entradas.map(entrada => {
                return entrada.evento &&( <td className="text-center">{ entrada.tipo }</td>)
            })} */}
            {/* <td className="text-center"><button className='btn btn-primary' onClick={()=>handleGoAsociar(item.uid)}>Asociar/Cambiar</button></td> */}
            <td className="text-center">{ item.usuario.nombre+" "+item.usuario.apellido }</td>
            {item.usuario._id === uid ? <td className="text-center"><button className='btn btn-warning' onClick={()=>handleGoEditar(item.uid)}>Editar</button></td> : <td className="text-center">-</td>}
            {item.usuario._id === uid ? <td className="text-center"><button className='btn btn-danger' onClick={()=>handleGoDesasociar(item.uid)}><i className="fas fa-trash-alt"></i></button></td> : <td className="text-center">-</td>}
          </tr>
        );
      })}
    </tbody>
  </table>}
  </> );
};
