import React from 'react';
import { Page, Text, View, Document, Image} from '@react-pdf/renderer';

export const ComprobanteDetalle = ({datos}) => {
    return (
      <Document>
        <Page
          size="A4"
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "start",
            alignItems: "center",
            backgroundColor: "white",
          }}
        >
          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "start",
              alignItems: "center",
              backgroundColor: "white",
              padding: 5,
            }}
          > 
            <Text style={{ marginTop:"35px",fontSize: "45px" }}>Evento</Text>
            <Text style={{ color: "#08298A",fontSize: "45px",marginBottom:"10px" }}>{datos.evento ? datos.evento : ""}</Text>
            <Text style={{ fontSize: "35px" }}>Fecha</Text>
            <Text style={{ color: "#08298A",fontSize: "35px",marginBottom:"5px" }}>{datos.fecha ? datos.fecha : ""}</Text>
            <Text style={{ fontSize: "30px", marginBottom:"5px" }}>Detalle</Text>
            <Text style={{ fontSize: "20px" }}>Ganancia Total</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"10px" }}>{datos.gan ? datos.gan : ""}</Text>
            <Text style={{ fontSize: "20px" }}>Deuda Total</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"10px" }}>{datos.deuTotal ? datos.deuTotal : ""}</Text>
            <Text style={{ fontSize: "20px" }}>Ganancia anticipadas</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"10px" }}>{datos.ganTotal ? datos.ganTotal : ""}</Text>
            <Text style={{ fontSize: "20px" }}>Ganancia puerta</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"10px" }}>{datos.ganPuerta ? datos.ganPuerta : ""}</Text>
            <Text style={{ fontSize: "20px" }}>Cantidad de Clientes</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"10px" }}>{datos.cantCli ? datos.cantCli : ""}</Text>
            {/* <Text style={{ fontSize: "20px" }}>Cantidad de RRPP</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"10px" }}>{datos.cantRrpp ? datos.cantRrpp : ""}</Text> */}
          </View>
        </Page>
      </Document>
    );
};
