import React from "react";
import { useForm } from "../../hooks/useForm";
import eventIcon from '../../images/ee.png';
import "./login.css";
import {isMobile} from 'react-device-detect';
import { fetchSinToken } from "../../helpers/fetch";
import Swal from "sweetalert2";
import { useHistory } from 'react-router-dom';

export const ClienteFreeLogin = () => {
  const history = useHistory();
  const [formLoginValues, handleLoginInputChange] = useForm({
    cId: "",
  });
  const { cId } = formLoginValues;

  const handleLogin = (e) => {
    e.preventDefault();
    const loginCliente = async(id) =>{
      const resp = await fetchSinToken("clientes/ingresofree", { id }, "POST");
      const body = await resp.json();
      if (body.ok) {
        localStorage.setItem("token", body.token);
        localStorage.setItem("token-init-date", new Date().getTime());
        Swal.fire("Exito", "Codigo vlido, a continuacion actualice sus datos", "success");
        history.push(`/logincliente/${id}`);
      } else {
        Swal.fire("Error", body.msg, "error");
      }
    }
    loginCliente(cId.trim());
  };
  
  return (
    <>
    { isMobile ?
    <div className="login-container">
    <h2 className="d-flex align-items-center justify-content-center mt-4" style={{ color: "#0062cc" }}><img src={eventIcon} alt="" /></h2>
    <div className="row ">
      <div className="col-md-11 col-11 login-form-1 mt-5">
        <h3>Ingreso FREE</h3>
        <form onSubmit={handleLogin}>
        <div className="form-group">
              <input
                type="text"
                className="form-control mb-2"
                placeholder="Codigo"
                name="cId"
                value={cId}
                onChange={handleLoginInputChange}
              />
            </div>
          <div className="form-group d-flex justify-content-center">
            <input type="submit" className="btnSubmit" value="Ingresar" />
          </div>
        </form>
      </div>

    </div>
  </div> 
  : 
  <div className="login-container">
      <h2 className="d-flex align-items-center justify-content-center mt-4" style={{ color: "#0062cc" }}><img src={eventIcon} alt="" /></h2>
      <div className="row mt-4">
        <div className="col-md-6 col-6 me-5 login-form-3">
        <h3>Ingreso FREE</h3>
          <form onSubmit={handleLogin}>
            <div className="form-group">
              <input
                type="text"
                className="form-control mb-2"
                placeholder="Codigo"
                name="cId"
                value={cId}
                onChange={handleLoginInputChange}
              />
            </div>
            <div className="form-group d-flex justify-content-center">
              <input type="submit" className="btnSubmit" value="Ingresar" />
            </div>
          </form>
        </div>

      </div>
    </div>
  }
  </>
  );
};
