import React, { useState } from 'react';
import { useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { RppItems } from './RppItems';
import { fetchConToken } from '../../helpers/fetch';
import { Pagination } from '../ui/Pagination';
import { AsociarModal } from './AsociarModal';
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from '../../hooks/useForm';
import {isMobile} from 'react-device-detect';
import { uiOpenModall } from '../../actions/ui';
import { eventClearIsFilter } from '../../actions/eventos';
import { AsociarEventoRppModal } from './AsociarEventoRppModal';
import { PDFDownloadLink } from '@react-pdf/renderer';
import { ComprobanteDetalleRRPP } from '../ui/ComprobanteDetalleRRPP';
import moment from 'moment';

export const RppScreen = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { uid } = useSelector((state) => state.auth);
  const [deuTotal, setDeuTotal] = useState(0);
  const [totalEntradas, setTotalEntradas] = useState(0);
  const [totalFavor, setTotalFavor] = useState(0);
  const { isFilter } = useSelector((state) => state.eventos);
  const { activeEvent } = useSelector((state) => state.eventos);
  const [planillaRrpp, setPlanillaRrpp] = useState(false);
  const [cantidadRpps, setCantidadRpps] = useState(0);
  //PAGINACION
  const [currentPage, setCurrentPage] = useState(1);
  const [rpps, setRpps] = useState([]);
  const [loading, setLoading] = useState(false);
  const [rppsPerPage, setRppsPerPage] = useState(10);
  const indexOfLastRpp = currentPage * rppsPerPage;
  const indexOfFirstRpp = indexOfLastRpp - rppsPerPage;
  const currentRpps = rpps.slice(indexOfFirstRpp,indexOfLastRpp);
  //FETCHING
  useEffect(() => {
    const getRpps = async() =>{
      const resp = await fetchConToken("auth/rpps");
      const body = await resp.json();
      setRpps(body.rpps);
      setPlanillaRrpp(true);
      setCantidadRpps(body.total);
    }
    const getRRPPsByEvento = async() =>{
      const resp = await fetchConToken(`auth/rrpps/${activeEvent.value}`);
      const body = await resp.json();
      setRpps(body.rpps);
    }
    const getGananciasByAdmin = async() =>{
      setLoading(true);
      const resp = await fetchConToken(`auth/rpps/deudad/${uid}`);
      const body = await resp.json();
      setDeuTotal(body.deutotal);
      setTotalEntradas(body.totalentradas);
      setTotalFavor(body.totalentradas-body.deutotal);
      getRpps();
      setLoading(false);
    }
    getGananciasByAdmin();
    if(isFilter){
      getRRPPsByEvento();
    }else{
      // getRpps();
    }
    
  }, [uid,activeEvent,isFilter]);
  const handleVolver = (e) => {
    history.push('/');
  };

  const [formSearchValues, handleSearchInputChange] = useForm({
    cSearch:""
  });
  const { cSearch } = formSearchValues;
  const handleBuscar = (e) => {
    e.preventDefault();
    const searchRpp = async() =>{
      const resp = await fetchConToken(`auth/search/${cSearch}`);
      const body = await resp.json();
      setRpps(body.rpps);
    }
    searchRpp();
  };
  const handleGoFilter = () => {
    dispatch(uiOpenModall());
  };
  const handleGoLimpiar= () => {
    dispatch(eventClearIsFilter());
  };

  return ( 
    <div id="rpp" className="container">
      {isMobile 
      ?
      <div className="d-flex flex-column justify-content-center align-items-center mb-3">
        <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleBuscar}>
          <input className='form-control me-2 text-center' title='Busqueda por apellido' placeholder='Ingrese el apellido...' type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
          <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
        </form>
        { planillaRrpp &&<PDFDownloadLink
                    document={ <ComprobanteDetalleRRPP rrpps={rpps} datos={{  fecha:moment().format('DD-MM-yyyy'),cantRrpp:rpps.length}}/>}
                    fileName="planilla-rrpps.pdf"
                    className="text-decoration-none"
                >
                    <div className="form-group d-flex justify-content-center">
                        <button className="btn btn-outline-primary mt-2 mb-2" type="submit"><i className="fas fa-print"></i> Imprimir planilla RRPPs</button>
                    </div>
                </PDFDownloadLink>}
        <div className='d-flex justify-content-center align-items-center mb-3'>
          <div className='form-group text-center me-2'>
            <label><b>Deuda total a todos los rpps</b></label>
            <input disabled={true} className='form-control' type="text" value={ loading ? 'Cargando...' : `$ ${deuTotal?deuTotal:0}`}/>
          </div>
        </div>
        <Link className="w-25 d-flex justify-content-end text-decoration-none" to="/addrpp">
          <button className="btn btn-success" title="Cargar un nuevo RPP">
            <i className="fas fa-user-plus"></i> RPP
          </button>
        </Link>
      </div>
      : 
      <div className="d-flex flex-column justify-content-between align-items-center mb-3">
        <div className="d-flex flex-row justify-content-between align-items-center mb-3 w-100">
        <form className='form-group d-flex justify-content-center align-items-center mb-2' onSubmit={handleBuscar}>
          <input className='form-control me-2 text-center' title='Busqueda por apellido' placeholder='Ingrese el apellido...' type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
          <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
        </form>
        
        <div className='d-flex justify-content-center align-items-center w-25'>
          <div className='form-group text-center me-2'>
            <label><b>Deuda total a todos los rpps</b></label>
            <input disabled={true} className='form-control' type="text" value={ loading ? 'Cargando...' : `$ ${deuTotal?deuTotal:0}`}/>
          </div>
        </div>
        <Link className="w-25 d-flex justify-content-end text-decoration-none" to="/addrpp">
          <button className="btn btn-success" title="Cargar un nuevo RPP">
            <i className="fas fa-user-plus"></i> RPP
          </button>
        </Link>
        </div>
        { planillaRrpp &&<PDFDownloadLink
                    document={ <ComprobanteDetalleRRPP rrpps={rpps} datos={{ fecha:moment().format('DD-MM-yyyy'),cantRrpp:rpps.length,totalDeuda:deuTotal,totalEntradas:totalEntradas,totalFavor:totalFavor}}/>}
                    fileName="planilla-rrpps.pdf"
                    className="text-decoration-none"
                >
                    <div className="form-group d-flex justify-content-center">
                        <button className="btn btn-outline-primary mt-2 mb-2" type="submit"><i className="fas fa-print"></i> Imprimir planilla RRPPs</button>
                    </div>
                </PDFDownloadLink>}
      </div>
      }
      
      <div className="d-flex flex-column justify-content-center align-items-center">
        <h4>Cantidad de RPP</h4>
        <h3 className='border p-2 mb-3 '>{rpps.length}</h3>
        <div className='d-flex flex-row'>
        <button className="btn btn-primary mb-2 me-2" onClick={handleGoFilter} title="Filtrar por Evento">
          <i className="fas fa-filter"></i> Filtrar por Evento
        </button>
        {isFilter &&<button className="btn btn-outline-danger mb-2 ms-2" onClick={handleGoLimpiar} title="Sacar filtro">
          <i className="fas fa-close"></i> Filtrado por evento: {activeEvent.label}
        </button>}
        </div>
        <RppItems rpps={currentRpps} loading={loading}/>
        <AsociarModal />
        <AsociarEventoRppModal />
        <Pagination dataPerPage={rppsPerPage} totalData={rpps.length} data={rpps} setCurrentPage={setCurrentPage}/>
        <div className="d-flex justify-content-center align-items-center mb-3">
              <button className="btn btn-primary mt-2" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
        </div>
      </div>
    </div>
  );
};
