import { types } from "../types/types";

const initialState = {
  clienteAsoc: null,
  cargando:false,
};


export const clienteReducer = (state = initialState, action) => {
    switch (action.type) {
      case types.clienteAsociar:
        return {
          ...state,
          clienteAsoc: action.payload
        };
        case types.startClearClienteAsoc:
        return {
          ...state,
          clienteAsoc: action.payload
        };
      case types.setLoading:
        return {
          ...state,
          cargando: true,
      };
      case types.setClientes:
        return {
          ...state,
          clientes:action.payload
      };
      case types.pasarBody:
        return {
          ...state,
          body:action.payload
      };
      case types.borrarBody:
        return {
          ...state,
          body:null
      };
      default:
        return state;
    }
  };
  