import Swal from "sweetalert2";
import { fetchConToken, fetchSinToken } from "../helpers/fetch";
import { types } from "../types/types";

export const startLogin = (emaill, password) => {
  return async (dispatch) => {
    let email = emaill.toLowerCase();
    const resp = await fetchSinToken("auth", { email, password }, "POST");
    const body = await resp.json();
    if (body.ok) {
      localStorage.setItem("token", body.token);
      localStorage.setItem("token-init-date", new Date().getTime());
      dispatch(login({ uid: body.uid, nombre: body.nombre, rol: body.rol, maxFree: body.maxFree }));
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};
export const startRegister = (dni,email, password, nombre, apellido, comisionEnt,roll,comisionRRPP,rrppp,maxFree) => {
  return async (dispatch) => {
    const rol = roll.value;
    const estado = true;
    const rrpp = rrppp.value;
    //const evento = eventoo.value;
    const resp = await fetchConToken(
      "auth/new",
      { email, password, nombre, apellido, rol, dni ,estado, comisionEnt, comisionRRPP,rrpp,maxFree},
      "POST"
    );
    const body = await resp.json();
    if (body.ok) {
      // dispatch(Asociar(body.uid, evento));
      Swal.fire("Exito", "RPP Cargado con exito", "success");
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

export const startSetRrppActive = (id) => {
  return async (dispatch) => {
    const resp = await fetchConToken(`auth/edit/${id}`);
    const body = await resp.json();
    dispatch(startAsociar(body.usuario));
  };
};

export const startUpdateRrpp = (dni, email, nombre, apellido, comisionEnt,comisionRRPP,maxFree) => {
  return async (dispatch) => {
    const resp = await fetchConToken(
      `auth/editt`,
      {dni, email, nombre, apellido, comisionEnt,comisionRRPP,maxFree },
      "PUT"
    );
    const body = await resp.json();
    if (body.ok) {
      Swal.fire("Exito", "Actualizado con xito", "success");
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};


