import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import {  useHistory } from 'react-router-dom';
import { eventSetActive } from '../../actions/eventos';
import { fetchConToken } from '../../helpers/fetch';

export const EventoElegir = () => {
    const { rol } = useSelector((state) => state.auth);
    const history = useHistory();
    const [eventos, setEventos] = useState([]);
    const dispatch = useDispatch();
    useEffect(() => {
        const getEventos = async() =>{
            const resp = await fetchConToken(`eventos`);
            const body = await resp.json();
            setEventos(body.eventos);
        }
        getEventos();
    }, [rol]);

    const handleEvento = (evento) => {
        dispatch(eventSetActive(evento));
        history.push(`/verificar/${evento.id}`);
    };
    const handleVolver = (e) => {
        history.push('/');
    };
    return (
    <div className='container'>
        <div className='d-flex justify-content-center align-items-center flex-row mb-5'>
        {eventos.map(evento => {
        return ( 
                <div role="button" key={evento.id} onClick={()=>handleEvento(evento)}>
                    <div className="card me-3">
                        <div className="card-body">
                            <h5 className="card-title">{evento.nombre}</h5>
                            <p className="card-text">{evento.ubicacion}</p>
                        </div>
                    </div>
                </div>
        )
        })}
        </div>
        <div className="d-flex justify-content-center align-items-center">
            <button className="btn btn-primary mt-2" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
        </div>
    </div>
    );
};
