const initialState = {
  // rpps: [],
  activeEvent: null,
  currentPage:1,
  eventos:[],
  precios:null,
  isFilter:false,
};

export const eventoReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.eventSetActive:
      return {
        ...state,
        activeEvent: action.payload,
      };
    case types.eventAddNew:
      return {
        ...state,
        eventos: [...state.events, action.payload],
      };
    case types.eventClearActiveEvent:
      return {
        ...state,
        activeEvent: null,
      };
    case types.eventUpdate:
      return {
        ...state,
        eventos: state.events.map((e) =>
          e.id === action.payload.id ? action.payload : e
        ),
      };
    case types.eventDelete:
      return {
        ...state,
        eventos: state.events.filter((e) => e.id !== state.activeEvent.id),
        activeEvent: null,
      };
    case types.eventosLoaded:
      return {
        ...state,
        eventos: [...action.payload.eventos],
      };
      case types.eventosByRppLoaded:
      return {
        ...state,
        eventos: [...action.payload.eventosRpp],
      };
    case types.setPrice:
      return {
        ...state,
        precios:[action.payload],
      };
    case types.rppLoaded:
      return {
          ...state,
          rpps: [...action.payload.rpps],
          total: action.payload.total
      };
    case types.eventLogout:
      return {
        ...state,
        eventos: [],
      };
    case types.currentPage:
      return {
        ...state,
        currentPage:action.payload
      };
    case types.isFilter:
        return {
          ...state,
          isFilter:true
        };
    case types.ClearisFilter:
        return {
          ...state,
          isFilter:false
        };

    default:
      return state;
  }
};
