import React, { useState } from 'react';
import QRCode from "qrcode.react";
import { useEffect } from 'react';
import { fetchConToken } from '../../helpers/fetch';

export const QRPuerta = ({evento}) => {
  const [hashed, setHashed] = useState("");
  useEffect(() => {
    const hashIt = async()=>{
      const resp = await fetchConToken(`eventos/sethashpuerta/${evento}`);
      const body = await resp.json();
      setHashed(body.hash);
    }
    hashIt();
  }, [evento]);
  return (
  <div>
      <QRCode className='d-none' value={`${hashed}`} size={128} level="H" />
  </div>
  );
};
