import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useHistory, useLocation } from 'react-router-dom';
import { borrarBody, startActualizarCliente, startActualizarClienteFree, startCargarCliente, startCargarClienteFree } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';
import moment from "moment";
// import DateTimePicker from "react-datetime-picker";
import Select from 'react-select';
import { useEffect } from 'react';
import { getPrecios, setEventos } from '../../actions/eventos';
import { QRGenerator } from '../ui/QRGenerator';
import { fetchConToken } from '../../helpers/fetch';
import Swal from 'sweetalert2';
import { startCheckingWsp } from '../../actions/ui';
import { useFormAuto } from '../../hooks/useFormAuto';

const now = moment();

export const AddClienteFreeList = () => {
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const [disableInput, setDisableInput] = useState(false);
  const [actualizar, setActualizar] = useState(false);
  const [mostrarEstadoWsp, setMostrarEstadoWsp] = useState(false);
  const qrCodeCanvas = document.querySelector('canvas');
  const dataUrl = qrCodeCanvas&&qrCodeCanvas.toDataURL('image/jpg', 0.3);
  const { uid } = useSelector((state) => state.auth);
  const { estadoWsp } = useSelector((state) => state.ui);
  const { body } = useSelector((state) => state.clientes);
  const history = useHistory();
  const dispatch = useDispatch();
  const [formClienteValues, handleClienteInputChange,reset,handleInput] = useForm({
      cNombre:"",
      cApellido:""
  });
  const [formAutoValues, handleAutoInputChange] = useFormAuto({
    cDni:"",
  });
  const [formCelularValues, handleCelularInputChange,reset1,handleInput1,handleInput2] = useForm({
    cCelular: "",
  });
  const {cCelular} = formCelularValues;
  const { cNombre, cApellido } = formClienteValues;
  const { cDni } = formAutoValues;
  const { activeEvent } = useSelector((state) => state.eventos);
  const [evento, setEvento] = useState({
    value:idEvento,
    label:activeEvent.nombre,
  });
  const [tipo, setTipo] = useState({
    value:0,
    label:'Free',
  });
  useEffect(() => {
    dispatch(setEventos());
    if(body){
      if(body.status==="error"){
        reset();
        reset1();
        dispatch(borrarBody());
        setDisableInput(false);
      }else{
        const nombre = document.getElementById('cNombre');
        const apellido = document.getElementById('cApellido');
        const celular = document.getElementById('cCelular');
        handleInput(apellido,body.cliente.apellido,nombre,body.cliente.nombre,celular,body.cliente.celular);
        handleInput2(celular,body.cliente.celular);
        setDisableInput(true);
        setActualizar(true);
        // Swal.fire("Exito", "El cliente existe en la base de datos", "success");
        dispatch(borrarBody());
      }
    }
  }, [dispatch,body]);
  useEffect(() => {
    setTimeout(function(){
      dispatch(startCheckingWsp());
    }, 1000);
    setTimeout(function(){
      dispatch(startCheckingWsp());
    }, 5000);
  }, [dispatch]);
  const handleCargar = (e) => {
      e.preventDefault();
      if (cCelular === "" || cDni === "" || cApellido === "" || cNombre === "" || evento ==="" || tipo === "") {
        return Swal.fire(
          "Error",
          "Debes de rellenar todos los campos",
          "error"
        );
      }
      if(actualizar){
        dispatch(startActualizarClienteFree(cDni, cCelular, now.toDate(), uid,evento,tipo,dataUrl));
        setMostrarEstadoWsp(true);
        setDisableInput(false);
        setActualizar(false);
      }else{
        dispatch(startCargarClienteFree(cDni, cNombre.trim(), cApellido.trim(), cCelular, now.toDate(), true, uid,evento,tipo,dataUrl));
        setMostrarEstadoWsp(true);
        setDisableInput(false);
        setActualizar(false);
      }
  };
  const handleVolver = (e) => {
    history.goBack();
  };
  const handleWsp= () => {
    const fileUpload = async(dni,evento)=>{
      const resp = await fetchConToken(
        "clientes/fileupload",
        { dni,evento },
        "POST"
      );
      const body = await resp.json();
      if(body.status==="ok"){
        Swal.fire({title:"Exito",text:body.msg, icon:"success",showConfirmButton:false});
        setTimeout(function(){
    
          const sendPdf = async() =>{
            const resp = await fetchConToken(
              `wsp/chat/sendpdf/${cCelular}`,
              {dni:cDni},
              'POST'
            );
            const body = await resp.json();
            if(body.status==="error"){
              Swal.fire("Error", body.message, "error");
              dispatch(startCheckingWsp());
            }else{
              Swal.fire("Exito", body.message, "success");
              dispatch(startCheckingWsp());
            }
          }
          if(estadoWsp==="Conectado"){
            Swal.fire({
              title: 'Estas seguro de enviar el PDF a este numero +54 9 '+cCelular+'?',
              icon: 'warning',
              showCancelButton: true,
              cancelButtonText: 'Cancelar',
              confirmButtonColor: '#3085d6',
              cancelButtonColor: '#d33',
              confirmButtonText: 'Si, enviar!'
            }).then((result) => {
              if (result.isConfirmed) {
                sendPdf();
                dispatch(startCheckingWsp());
              }
            })
          }else{
            Swal.fire('Error','Ningun celular esta conectado al servidor','error');
          }
        }, 2000);
      }else{
        Swal.fire("Error", body.msg, "error");
      }
    }
    fileUpload(cDni,evento.value);    
  };
  const [symbolsArr] = useState(["e", "E", "+", "-", ".",","]);
  return ( 
  <div className="col-md-6 login-form-2 mb-4">
    <div className='d-flex justify-content-center mb-2'><span className='bg-light rounded'>Estado <i style={{"fontSize":"1.1rem","color":"green","marginLeft":"-8px","marginRight":"-5px"}} className="py-3 px-2 fab fa-whatsapp fa-xs position-sticky"></i>: <b className={estadoWsp==="Conectado"?'text-success':'text-danger'}>{estadoWsp}</b></span></div>
  <h3>Cargar cliente FREE</h3>
  <hr style={{color:"white"}}/>
  {/* <div className='d-flex justify-content-center mb-2'>
        <button className='btn btn-success' onClick={handleVerificar}>Verificar DNI</button>
      </div> */}
  <form onSubmit={handleCargar}>
    <div className="form-group mb-4">
      <input
        type="number"
        maxLength={8}
        minLength={7}
        title="Debe poner 7 digitos como minimo o 8 digitos como maximo"
        className="form-control mb-2 border border-primary text-center"
        placeholder="DNI"
        name="cDni"
        value={cDni}
        autoComplete="off"
        onChange={(e)=>handleAutoInputChange(e)}
        onKeyDown={e => symbolsArr.includes(e.key) && e.preventDefault()}
      />
      
    </div>
    <div className="form-group">
      <input
        type="number"
        className="form-control mb-2 text-center"
        placeholder="Celular"
        name="cCelular"
        id="cCelular"
        value={cCelular}
        onChange={handleCelularInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder="Nombre"
        name="cNombre"
        id="cNombre"
        value={cNombre}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>

    <div className="form-group mb-4">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder="Apellido"
        name="cApellido"
        id="cApellido"
        value={cApellido}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Cargar cliente FREE" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
  <QRGenerator uid={uid} dni={cDni}/>
  {mostrarEstadoWsp && estadoWsp!=="Desconectado" &&
    <div className="form-group d-flex justify-content-center align-items-center">
      <button className="btn btn-success mt-2 d-flex justify-content-center align-items-center" onClick={handleWsp}><i style={{"fontSize":"1.5rem"}} className="py-0 px-2 fab fa-whatsapp fa-xs position-sticky"></i> Enviar PDF por WSP</button>
    </div>
  }
</div>);
 
};