import React from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import Swal from 'sweetalert2';
import { startUpdateRrpp } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';
import "./../auth/login.css";
import { useSelector } from 'react-redux';

export const RrppEditar = () => {
  const { userAsoc } = useSelector((state) => state.auth);
  const [formRrppValues, handleRrppInputChange] = useForm({
      rNombre:userAsoc ? userAsoc.nombre: "",
      rApellido:userAsoc ? userAsoc.apellido: "",
      rEmail: userAsoc ? userAsoc.email:"",
      // rPassword:userAsoc ? userAsoc.celular: "",
      rComision:userAsoc ? userAsoc.comisionEnt:"",
      rComisionTarjetero:userAsoc ? userAsoc.comisionRRPP:"",
      rCantidadFree:userAsoc ? userAsoc.maxFree:""
  });
  const dispatch = useDispatch();
  const history = useHistory();
  const { rNombre, rApellido, rEmail, rComision,rComisionTarjetero,rCantidadFree } = formRrppValues;

  const handleUpdate = (e) => {
        e.preventDefault();
        if ( rNombre === "" || rApellido === "" || rEmail === "" || rComision==="" || rCantidadFree==="") {
          return Swal.fire(
            "Error",
            "Debes de rellenar todos los campos",
            "error"
          );
        }
        dispatch(startUpdateRrpp( userAsoc.dni, rEmail, rNombre, rApellido, rComision,rComisionTarjetero,rCantidadFree));
        setTimeout(()=>{
          history.goBack();
        },500);
  };
  const handleVolver = (e) => {
        history.push('/controlrpp');
  };
  return ( 
  <div className="col-md-6 login-form-2">
  <h3> Actualizar RPP</h3>
  {userAsoc && <h5 className="text-center" style={{color:"white"}}> DNI - {userAsoc.dni}</h5> }
  <form onSubmit={handleUpdate}>
    <div className="form-group text-center">
      <label style={{color:"white"}}>Nombre</label>
      <input
        type="text"
        className="form-control mb-2"
        placeholder="Nombre"
        name="rNombre"
        value={rNombre}
        onChange={handleRrppInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Apellido</label>
      <input
        type="text"
        className="form-control mb-2"
        placeholder="Apellido"
        name="rApellido"
        value={rApellido}
        onChange={handleRrppInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Email</label>
      <input
        type="email"
        className="form-control mb-2"
        placeholder="Correo"
        name="rEmail"
        value={rEmail}
        autoComplete="new-password"
        onChange={handleRrppInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Comision</label>
      <input
        type="number"
        title="Comision"
        className="form-control mb-2"
        placeholder="Comision de Entradas de cada RRPP o TARJETERO"
        name="rComision"
        value={rComision}
        onChange={handleRrppInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Cantidad de FREE</label>
      <input
        type="number"
        title="Cantidad Free"
        className="form-control mb-2"
        placeholder="Cantidad de clientes FREE de este usuario"
        name="rCantidadFree"
        value={rCantidadFree}
        onChange={handleRrppInputChange}
      />
    </div>
    {userAsoc.rol==="RRPP"&&<div className="form-group text-center">
    <label style={{color:"white"}}>Comision de RRPP sobre Tarjeteros</label>
      <input
        type="number"
        title="Comision"
        className="form-control mb-2"
        placeholder="Comision que cobra el RRPP a los TARJETEROS"
        name="rComisionTarjetero"
        value={rComisionTarjetero}
        onChange={handleRrppInputChange}
      />
    </div>}

    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Actualizar" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
</div>);
};
