import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { borrarBody, startAutoActualizarFree, startAutoCargaFree } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';
import moment from "moment";
import { useEffect } from 'react';
import Swal from 'sweetalert2';
import { useFormAuto } from '../../hooks/useFormAuto';
import { useLocation } from 'react-router-dom';
import { startLogout } from '../../actions/auth';
import { QRGenerator } from '../ui/QRGenerator';
import { PDFDownloadLink } from '@react-pdf/renderer';
import { Comprobante } from '../ui/Comprobante';
import { fetchConToken, fetchSinToken } from '../../helpers/fetch';
import { QRGeneratorSinToken } from '../ui/QRGeneratorSinToken';

const now = moment();

export const ClienteFreeCarga = () => {
  const location = useLocation();
  const idCliente = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const qrCodeCanvas = document.querySelector('canvas');
  const dataUrl = qrCodeCanvas&&qrCodeCanvas.toDataURL('image/jpg', 0.3);
  const [disableInput, setDisableInput] = useState(false);
  const [actualizar, setActualizar] = useState(false);
  const [generarPDF, setGenerarPDF] = useState(false);
  const [uid, setUid] = useState("");
  const [evento, setEvento] = useState("");
  const [tipo, setTipo] = useState("")
  const { body } = useSelector((state) => state.clientes);
  const history = useHistory();
  const dispatch = useDispatch();
  const [formClienteValues, handleClienteInputChange,reset,handleInput] = useForm({
      cNombre:"",
      cApellido:""
  });
  const [formAutoValues, handleAutoInputChange] = useFormAuto({
    cDni:"",
  });
  const [formCelularValues, handleCelularInputChange,reset1,handleInput1,handleInput2] = useForm({
    cCelular: "",
  });
  const {cCelular} = formCelularValues;
  const { cNombre, cApellido } = formClienteValues;
  const { cDni } = formAutoValues;
  useEffect(() => {
    if(body){
      if(body.status==="error"){
        reset();
        reset1();
        dispatch(borrarBody());
        setDisableInput(false);
      }else{
        const nombre = document.getElementById('cNombre');
        const apellido = document.getElementById('cApellido');
        const celular = document.getElementById('cCelular');
        handleInput(apellido,body.cliente.apellido,nombre,body.cliente.nombre,celular,body.cliente.celular);
        handleInput2(celular,body.cliente.celular);
        setDisableInput(true);
        setActualizar(true);
        dispatch(borrarBody());
      }
    }
  }, [dispatch,body,reset,reset1,handleInput,handleInput2]);
  useEffect(() => {
    const getcliente = async()=>{
      const resp = await fetchSinToken(`clientes/edit/${idCliente}`);
      const body = await resp.json();
      setUid(body.cliente.uid);
      setEvento(body.cliente.evento);
      setTipo(body.cliente.tipo);
    }
    getcliente()
  }, [idCliente])
  
  const handleCargar = (e) => {
      e.preventDefault();
      if (cCelular === "" || cDni === "" || cApellido === "" || cNombre === "") {
        return Swal.fire(
          "Error",
          "Debes de rellenar todos los campos",
          "error"
        );
      }
      if(actualizar){
        // dispatch(startAutoActualizarFree(idCliente,cDni, cCelular, now.toDate()));
        const autoActualizarFree = async(id,dni,celular,fecha)=>{
          const resp = await fetchConToken(
            "clientes/autoupdatefree",
            { id,dni,celular,fecha },
            "PUT"
          );
          const body = await resp.json();
          if (body.ok) {
            Swal.fire("Exito", "Cliente nuevo cargado con xito", "success");
            setTimeout(()=>{
              setGenerarPDF(true);
              const el = document.getElementsByClassName('downloadpdfwsp');
              for(let i =0;i<el.length;i++){
                el[i].click();
              }
              dispatch(startLogout());
              history.push('/logincliente');
            },1000)
          }else{
            Swal.fire("Error", body.msg, "error");
            dispatch(startLogout());
            history.push('/logincliente');
          }
        }
        autoActualizarFree(idCliente,cDni, cCelular, now.toDate());
        
      }else{
        // dispatch(startAutoCargaFree(idCliente,cDni,cNombre,cApellido, cCelular, now.toDate()));
        const autoCargarFree = async(id,dni,nombre,apellido, celular, fecha)=>{
          const resp = await fetchConToken(
            "clientes/autocargafree",
            { id,dni,nombre,apellido, celular, fecha },
            "PUT"
          );
          const body = await resp.json();
          if (body.ok) {
            Swal.fire("Exito", "Cliente nuevo cargado con xito", "success");
            setTimeout(()=>{
              setGenerarPDF(true);
              const el = document.getElementsByClassName('downloadpdfwsp');
              for(let i =0;i<el.length;i++){
                el[i].click();
              }
              dispatch(startLogout());
              history.push('/logincliente');
            },1000)
          }else{
            Swal.fire("Error", body.msg, "error");
            dispatch(startLogout());
            history.push('/logincliente');
          }
        }
        autoCargarFree(idCliente,cDni,cNombre,cApellido, cCelular, now.toDate());
        
       
      }
  };
  const [symbolsArr] = useState(["e", "E", "+", "-", ".",","]);
  return ( 
    <div className="d-flex justify-content-center align-items-center flex-column">
  <div className="col-md-6 login-form-2 mb-4 mt-1">
  <h3>Actualice sus datos para cargarse en el sistema</h3>
  <hr style={{color:"white"}}/>
  <form onSubmit={handleCargar}>
  <h3>Ingrese sus datos</h3>

    <div className="form-group mb-5">
      <input
        type="number"
        maxLength={8}
        minLength={7}
        title="Debe poner 7 digitos como minimo o 8 digitos como maximo"
        className="form-control mb-2 border border-primary text-center"
        placeholder="DNI"
        name="cDni"
        value={cDni}
        autoComplete="off"
        onChange={(e)=>handleAutoInputChange(e)}
        onKeyDown={e => symbolsArr.includes(e.key) && e.preventDefault()}
      />
      
    </div>
    <div className="form-group">
      <input
        type="number"
        className="form-control mb-2 text-center"
        placeholder="Celular"
        name="cCelular"
        id="cCelular"
        value={cCelular}
        onChange={handleCelularInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder="Nombre"
        name="cNombre"
        id="cNombre"
        value={cNombre}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>

    <div className="form-group">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder="Apellido"
        name="cApellido"
        id="cApellido"
        value={cApellido}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Cargar" />
    </div>
  </form>
  
</div>
<QRGeneratorSinToken uid={uid} dni={cDni}/>
    <PDFDownloadLink
        document={ <Comprobante datos={{uri:dataUrl,dni:cDni, nombre:cNombre, apellido:cApellido, celular:cCelular, fecha:moment().format('DD-MM-YYYY'), estado:true, uid:uid,evento:evento,tipo:tipo}}/>}
        fileName={`comprobante-${cDni}.pdf`}
        className="text-decoration-none downloadpdfwsp"
      >
        <div className="form-group d-flex justify-content-center">
          <button className="btnSubmit mt-2">Descargar PDF</button>
        </div>
  </PDFDownloadLink>
</div>
);
};
