import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/fetch";
import moment from "moment";
import { types } from "../types/types";


export const startCargarCliente = (dni,nombre,apellido,celular,fecha,estado,usuario,eventoo,tipoo,dataUrl) => {
    return async (dispatch) => {
      let tipo = tipoo.label;
      let evento = eventoo.label;
      const eventoid = eventoo.value;
      const free = false;
      const resp = await fetchConToken(
        "clientes",
        { dni,nombre,apellido,celular,fecha,estado,usuario,dataUrl,evento,tipo,free,eventoid },
        "POST"
      );
      const body = await resp.json();
      if (body.ok) {
        evento = eventoo;
        tipo = tipoo;
        dispatch(asociarCliente(body.cliente.uid, evento, tipo,usuario));
        Swal.fire("Exito", "Cliente nuevo cargado con xito", "success");
        
      } else {
        if(body.oki){
          dispatch(asociarCliente(body.cliente.uid, evento, tipo,usuario));
          Swal.fire("Exito", "Cliente asociado con xito", "success");
          
        }else{
        Swal.fire("Error", body.msg, "error");
        }
      }
    };
};

