export const startVerificarHash = (hash,evento) => {
  return async (dispatch) => {
    const resp = await fetchConToken(`clientes/hash/${hash}/${evento}`);
    const body = await resp.json();
    if (resp.status === 200) {
      let dni = `${body.cliente.dni}`;
      let nombre = `${body.cliente.nombre.toUpperCase()} ${body.cliente.apellido.toUpperCase()}`;
      let horarioent =`${moment(body.cliente.entrada).format('HH:mm')}`;
      Swal.fire({title:"INGRESO VERIFICADO", html:'<pre> <b>DNI</b> <br/>' + dni +'<br/> <b>Nombre completo</b> <br/>'+ nombre +'<br/> <b>Horario de entrada</b> <br/>'+ horarioent + '</pre>', icon:"success"}).then((result)=>{
        if(result.isConfirmed){
          const getClientesVerificados= async() =>{
            setLoading(true);
            const resp = await fetchConToken(`clientes/verificados/${evento}`);
            const body = await resp.json();
            setClientes(body.clientes);
            setLoading(false);
          }
          setTimeout(() => {
            getClientesVerificados();
          }, 500);
          // window.location.reload();
        }
      });
      // Swal.fire("Verificado",body.msg, "success");
      // dispatch(setLoading());
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

export const startActualizarCliente = (dni,celular,fecha,usuario,eventoo,tipoo,dataUrl) => {
  return async (dispatch) => {
    const evento = eventoo.label;
    const eventoid = eventoo.value;
    const tipo = tipoo.label; 
    const free = false;
    const resp = await fetchConToken(
      "clientes/update",
      { dni,celular,fecha,usuario,dataUrl,evento,tipo,free,eventoid },
      "POST"
    );
    const body = await resp.json();
    
    if (body.ok) {
      dispatch(asociarCliente(body.cliente.uid, eventoo, tipoo, usuario));
      Swal.fire("Exito", "Cliente actualizado con xito", "success");
      
    } else {
      if(body.oki){
        dispatch(asociarCliente(body.cliente.uid, eventoo, tipoo, usuario));
        Swal.fire("Exito", "Cliente asociado con xito", "success");
        
      }else{
        Swal.fire("Error", body.msg, "error");
      }
    }
  };
};

