export const startVerificarDni = (dni,evento) => {
    return async (dispatch) => {
      const resp = await fetchConToken(`clientes/dni/${dni}/${evento}`);
      const body = await resp.json();
      if (resp.status === 201) {
        let dni = `${body.cliente.dni}`;
        let nombre = `${body.cliente.nombre.toUpperCase()} ${body.cliente.apellido.toUpperCase()}`;
        let horarioent =`${moment(body.cliente.entrada).format('HH:mm')}`;
        let horariosal =`${moment(body.cliente.salida).format('HH:mm')}`;
        if(body.egreso){
          Swal.fire({title:"EGRESO", html:'<pre> <b>DNI</b> <br/>' + dni +'<br/> <b>Nombre completo</b> <br/>'+ nombre +'<br/> <b>Horario de salida</b> <br/>'+ horariosal + '</pre>', icon:"info"}).then((result)=>{
            if(result.isConfirmed){
              window.location.reload();
            }
          });
        }else{
          Swal.fire({title:"INGRESO", html:'<pre> <b>DNI</b> <br/>' + dni +'<br/> <b>Nombre completo</b> <br/>'+ nombre +'<br/> <b>Horario de entrada</b> <br/>'+ horarioent + '</pre>', icon:"success"});
        }
        dispatch(setLoading());
      } else {
        Swal.fire("Error", body.msg, "error");
      }
    };
};

export const getClientes = () => {
  return async (dispatch) => {
    const resp = await fetchConToken('clientes');
    const body = await resp.json();
    dispatch(setClientes(body.clientes));
  };
};

export const startSetActive = (id) => {
  return async (dispatch) => {
    const resp = await fetchConToken(`clientes/edit/${id}`);
    const body = await resp.json();
    dispatch(startAsociarCliente(body.cliente));
  };
};


export const startAsociarCliente = (uid) => ({
  type: types.clienteAsociar,
  payload: uid,
});

export const pasarBody = (body) => ({
  type: types.pasarBody,
  payload: body,
});

export const borrarBody = () => ({
  type: types.borrarBody,
  payload: null,
});


export const setClientes = (clientes) => ({
  type: types.setClientes,
  payload: clientes,
});

export const startClearClienteAsoc = () => ({
  type: types.startClearClienteAsoc,
  payload: null,
});

export const setLoading = () => ({
  type: types.setLoading,
  payload: null,
});

export const asociarCliente = (uid,eventoo,tipoo,usuario) => {
  return async (dispatch) => {
    const tipolabel = tipoo.label.substring(tipoo.label.lastIndexOf('-') + 1);
    const tipo = tipolabel.trim();
    const importe = tipoo.value;
    const evento = eventoo.value;
    let nombrevento = eventoo.label;
    const resp = await fetchConToken(
      'clientes/asociar',
      { uid, evento, tipo , importe, usuario,nombrevento },
      "POST"
    );
    const body = await resp.json();
    if (resp.status === 201) {
      //  Swal.fire("Exito", "Evento nuevo/cambiado con xito", "success");
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

export const startDesasociarCliente = (id,evento,uid) => {
  return async (dispatch) => {
    const resp = await fetchConToken(
      'clientes/desasociar',
      { uid, evento,id},
      "POST"
    );
    const body = await resp.json();
    if (resp.status === 201) {
      Swal.fire({title:"Exito", text:"Desasociado con xito", icon:"success",showConfirmButton:false});
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

