import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import {  startUpdateCliente } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';

export const ClienteEditar = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    const { clienteAsoc } = useSelector((state) => state.clientes);
    const [formClienteValues, handleClienteInputChange] = useForm({
        cNombre: clienteAsoc ? clienteAsoc.nombre:"",
        cApellido: clienteAsoc ? clienteAsoc.apellido:"",
        cCelular:clienteAsoc ? clienteAsoc.celular:"",
    });
    const { cNombre, cApellido, cCelular } = formClienteValues;
    const handleUpdateCliente = (e) => {
        e.preventDefault();
        dispatch(startUpdateCliente(clienteAsoc.dni, cCelular));
            setTimeout(()=>{
                history.goBack();
            },500);
    };
    const handleVolver = (e) => {
        history.push('/');
    };
  return (
  <div>
    
    <div className='container'>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
            <label className='form-label h3'><b>DNI</b></label>
            {clienteAsoc && <h3 className='text-dark'>{clienteAsoc.dni}</h3>}
        </div>
        <form onSubmit={handleUpdateCliente}>
        {/* <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>Nombre</b></label>
            <div><input className='border rounded-pill text-center form-control' name="cNombre" type="text" value={cNombre} onChange={handleClienteInputChange}/></div>
        </div>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>Apellido</b></label>
            <div><input className='border rounded-pill text-center form-control' name="cApellido" type="text" value={cApellido} onChange={handleClienteInputChange}/></div>
        </div> */}
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-3'>
        <label className='form-label h5'><b>Celular</b></label>
            <div><input className='border rounded-pill text-center form-control' name="cCelular" type="text" value={cCelular} onChange={handleClienteInputChange}/></div>
        </div>
        <div className="form-group d-flex justify-content-center mb-2">
            <button type="submit" className="btn btn-success" ><i className="fas fa-check"></i> Actualizar</button>
        </div>
        </form>
        <div className="form-group d-flex justify-content-center">
    <button className="btn btn-primary mb-5" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
  </div>
    </div>
  </div>
  );
};
