import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/fetch";
import { prepareEvents } from "../helpers/prepareEvents";
import { types } from "../types/types";

export const eventStartAddNew = (event) => {
  return async (dispatch, getState) => {
    const { uid, name } = getState().auth;
    try {
      const resp = await fetchConToken("events", event, "POST");
      const body = await resp.json();
      if (body.ok) {
        event.id = body.evento.id;
        event.user = {
          _id: uid,
          name,
        };
        dispatch(eventAddNew(event));
      } else {
        Swal.fire("Error", body.msg, "error");
      }
    } catch (error) {
      console.log(error);
    }
  };
};

const eventAddNew = (event) => ({
  type: types.eventAddNew,
  payload: event,
});
const eventUpdate = (event) => ({
  type: types.eventUpdate,
  payload: event,
});
export const eventSetActive = (event) => ({
  type: types.eventSetActive,
  payload: event,
});
export const eventIsFilter = () => ({
  type: types.isFilter
});
export const eventClearIsFilter = () => ({
  type: types.ClearisFilter
});
export const eventClearActiveEvent = () => ({
  type: types.eventClearActiveEvent,
});

export const eventStartUpdate = (event) => {
  return async (dispatch) => {
    try {
      const resp = await fetchConToken(`events/${event.id}`, event, "PUT");
      const body = await resp.json();
      if (body.ok) {
        dispatch(eventUpdate(event));
      } else {
        Swal.fire("Error", body.msg, "error");
      }
    } catch (error) {
      console.log(error);
    }
  };
};

export const startCargarEvento = (nombre, ubicacion, vip, preferencial, general, puerta,fecha,usuario,dataUrl ) => {
  return async (dispatch) => {
    const resp = await fetchConToken(
      "eventos",
      { nombre, ubicacion, vip, preferencial, general, fecha, usuario,puerta,dataUrl },
      "POST"
    );
    const body = await resp.json();
    if (body.ok) {
      Swal.fire("Exito", "Cargado con xito", "success");
      dispatch(eventSetActive( body.evento ));
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

export const startActualizarEvento = (id,ubicacion, vip, preferencial, general,puerta,fecha ) => {
  return async (dispatch) => {
    const resp = await fetchConToken(
      `eventos/${id}`,
      { ubicacion, vip, preferencial, general, fecha,puerta },
      "PUT"
    );
    const body = await resp.json();
    if (body.ok) {
      Swal.fire("Exito", "Actualizado con xito", "success");
      //dispatch(addCliente({ uid: body.uid, name: body.name }));
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

export const eventStartDelete = () => {
  return async (dispatch, getState) => {
    const { id } = getState().calendar.activeEvent;
    try {
      const resp = await fetchConToken(`events/${id}`, {}, "DELETE");
      const body = await resp.json();
      if (body.ok) {
        dispatch(eventDelete());
        window.location.reload();
      } else {
        Swal.fire("Error", body.msg, "error");
      }
    } catch (error) {
      console.log(error);
    }
  };
};
const eventDelete = () => ({
  type: types.eventDelete,
});

export const eventStartLoading = () => {
  return async (dispatch) => {
    try {
      const resp = await fetchConToken("events");
      const body = await resp.json();
      const eventos = prepareEvents(body.eventos);

      dispatch(eventosLoaded(eventos));
    } catch (error) {
      console.log(error);
    }
  };
};

