import { types } from "../types/types";

const initialState = {
  modalOpen: false,
  modalOpenn: false,
  estadoWsp:"Desconectado"
};

export const uiReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.uiOpenModal:
      return {
        ...state,
        modalOpen: true,
      };
    case types.uiOpenModall:
      return {
        ...state,
        modalOpenn: true,
      };
    case types.uiCloseModal:
      return {
        ...state,
        modalOpen: false,
      };
    case types.uiCloseModall:
      return {
        ...state,
        modalOpenn: false,
      };
    case types.getEstadoWsp:
        return {
          ...state,
          estadoWsp:action.payload,
        }; 
    default:
      return state;
  }
};
