import React from 'react';
import { Page, Text, View, Document,StyleSheet} from '@react-pdf/renderer';

const styles = StyleSheet.create({
  table: {
    width: '100%'
  },
  row: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    borderTop: '1px solid #EEE',
    paddingTop: 8,
    paddingBottom: 8
  },
  header: {
    borderTop: 'none',
  },
  bold: {
    fontWeight: 'bold',
  },
})

export const ComprobanteDetalleClientesFree = ({datos,clientes}) => {  
    return (
      <Document>
        <Page
          size="A4"
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "start",
            alignItems: "center",
            backgroundColor: "white", 
            padding: 25
          }}
        >
          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "start",
              alignItems: "center",
              backgroundColor: "white",
              padding: 5,
            }}
          > 
            <Text style={{ marginTop:"35px",fontSize: "30px" }}>Evento</Text>
            <Text style={{ color: "#08298A",fontSize: "30px",marginBottom:"10px" }}>{datos.evento ? datos.evento : ""}</Text>
            <Text style={{ fontSize: "20px" }}>Fecha</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"5px" }}>{datos.fecha ? datos.fecha : ""}</Text>
            <Text style={{ fontSize: "20px" }}>Usuario</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"5px" }}>{datos.usuario ? datos.usuario : ""}</Text>
            <View style={styles.table}>
              <View style={[styles.row, styles.bold, styles.header]} fixed>
                <Text style={{width: '50%'}}>CODIGOS</Text>
              </View>
              {clientes.map(cliente => (
                <View key={cliente.uid} style={styles.row} wrap={false}>
                  <Text style={{width: '50%'}} >{cliente.uid}</Text>
                </View>
              ))}
            </View>
          </View>
        </Page>
      </Document>
    );
};
