import React, { useState } from 'react';
import QRCode from "qrcode.react";
import { useEffect } from 'react';
import { fetchConToken, fetchSinToken } from '../../helpers/fetch';
import { useDispatch } from 'react-redux';
import { setHasheo } from '../../actions/clientes';

export const QRGeneratorSinToken = ({uid,dni}) => {
  const [hashed, setHashed] = useState("");
  // const dispatch = useDispatch();  
  useEffect(() => {
    const hashIt = async()=>{
      const resp = await fetchSinToken(`clientes/sethashsintoken`,{uid,dni},'POST');
      const body = await resp.json();
      setHashed(dni+body.hash);
      // dispatch(setHasheo(dni+body.hash));
    }
    hashIt();
  }, [uid,dni]);
  return (
  <div>
      <QRCode className='d-none' value={`${hashed}`} size={128} level="H" />
  </div>
  );
};
