import React from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import Swal from 'sweetalert2';
import { startRegister } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';
import "./../auth/login.css";
import Select from 'react-select';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { setEventos } from '../../actions/eventos';
import { useEffect } from 'react';
import { fetchConToken } from '../../helpers/fetch';

export const AddRpp = () => {
  const [rrpps, setRRPPS] = useState();
  const [rrpp, setRRPP] = useState("");
  const [rol, setRol] = useState("");
  let rrppss = [];
  if(rrpps){
    for (let i = 0; i < rrpps.length; i++) {
      const rrpp = {
        value:rrpps[i].uid,
        label:rrpps[i].nombre+" "+rrpps[i].apellido
      }
      rrppss.push(rrpp);
    }
  }
  let roles = [
    { value: "RRPP", label: "RRPP" },
    { value: "TARJETERO", label: "TARJETERO" }
  ];
  const dispatch = useDispatch();
  const history = useHistory();
  const [formRegisterValues, handleRegisterInputChange] = useForm({
        rDni:"",
        rNombre: "",
        rApellido: "",
        rEmail: "",
        rPassword: "",
        rComision:"",
        rComisionTarjetero:"",
        rCantidadFree:""
    });
  const { rDni,rNombre, rApellido, rEmail, rPassword,rComision,rComisionTarjetero,rCantidadFree } = formRegisterValues;

  useEffect(() => {
    dispatch(setEventos());
    const getRpps = async() =>{
      const resp = await fetchConToken("auth/rpps/rpps");
      const body = await resp.json();
      setRRPPS(body.rpps);
    }
    getRpps();
  }, [dispatch]);

  const handleRegister = (e) => {
        e.preventDefault();
        if (rDni === "" || rNombre === "" || rApellido === "" || rEmail === "" || rPassword === "" || rComision==="" || rCantidadFree==="") {
          return Swal.fire(
            "Error",
            "Debes de rellenar todos los campos",
            "error"
          );
        }
        if (rDni.length <7 || rDni.length >8) {
          return Swal.fire(
            "Error",
            "El DNI tiene que tener entre 7 y 8 digitos",
            "error"
          );
        }
        dispatch(startRegister(rDni, rEmail, rPassword, rNombre, rApellido, rComision,rol,rComisionTarjetero,rrpp,rCantidadFree));
  };
  const handleVolver = (e) => {
        history.push('/controlrpp');
  };
  const handleRol = (e) => {
        setRol(e);
  };
  const handleRRPP = (e) => {
    setRRPP(e);
};
  return ( 
  <div className="col-md-6 login-form-2">
  <h3>Cargar RPP</h3>
  <form onSubmit={handleRegister}>
    <div className="form-group">
      <input
        type="number"
        maxLength={8}
        minLength={7}
        title="Debe poner 7 digitos como minimo o 8 digitos como maximo"
        className="form-control mb-2"
        placeholder="DNI"
        name="rDni"
        value={rDni}
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className="form-control mb-2"
        placeholder="Nombre"
        name="rNombre"
        value={rNombre}
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className="form-control mb-2"
        placeholder="Apellido"
        name="rApellido"
        value={rApellido}
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="email"
        className="form-control mb-2"
        placeholder="Correo"
        name="rEmail"
        value={rEmail}
        autoComplete="new-password"
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="password"
        className="form-control mb-2"
        placeholder="Contrasea"
        name="rPassword"
        value={rPassword}
        autoComplete="new-password"
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="number"
        title="Comision"
        className="form-control mb-2"
        placeholder="Comision de Entradas de cada RRPP o TARJETERO"
        name="rComision"
        value={rComision}
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="number"
        title="Cantidad Free"
        className="form-control mb-2"
        placeholder="Cantidad de clientes FREE de este usuario"
        name="rCantidadFree"
        value={rCantidadFree}
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label className='form-label' style={{color:"white"}}><b>Rol</b></label>
      <Select className="form-control mb-2" options={roles} value={rol} onChange={handleRol}/>
    </div>
    {rol.value==="RRPP"&&<div className="form-group">
      <input
        type="number"
        title="Comision"
        className="form-control mb-2"
        placeholder="Comision que cobra el RRPP a los TARJETEROS"
        name="rComisionTarjetero"
        value={rComisionTarjetero}
        onChange={handleRegisterInputChange}
      />
    </div>}
    {rol.value==="TARJETERO"&&<div className="form-group text-center">
    <label className='form-label' style={{color:"white"}}><b>Elegir RRPP para este TARJETERO</b></label>
      <Select className="form-control mb-2" options={rrppss} value={rrpp} onChange={handleRRPP}/>
    </div>}

    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Cargar" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
</div>);
};
