import React from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import { uiCloseModal } from "../../actions/ui";
import "../modalEliminar.css";
import { minusClientePuerta, startClearClienteAsoc } from "../../actions/clientes";
import { useForm } from "../../hooks/useForm";
import { useLocation } from "react-router-dom";
import { fetchConToken } from "../../helpers/fetch";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const PasswordModal = () => {
  const dispatch = useDispatch();
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const { modalOpen } = useSelector((state) => state.ui);
  const { uid } = useSelector((state) => state.auth);
  const [formPassValues, handlePassInputChange] = useForm({pass:""});
  const { pass } = formPassValues;

  const closeModal = () => {
    dispatch(uiCloseModal());
    dispatch(startClearClienteAsoc());
    window.location.reload();
  };
  const handlePass = () => {
    handleQuitarPuerta(uid,pass);
    closeModal();
  };

  const handleQuitarPuerta = (uid,pass) => {
    if(idEvento){
      const quitarPorPuerta= async() =>{
        const resp = await fetchConToken('eventos/delpuerta',{evento:idEvento,pass,uid},'POST');
        const body = await resp.json();
        if(body.ok){
        }
      }
      quitarPorPuerta();
    }
  };
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpen}
        onRequestClose={closeModal}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        ariaHideApp={process.env.NODE_ENV==="test"}
      > 
        <h1 className="black mt-5">Verifique su identidad</h1>
        <hr />
        <div className=" mb-2">
        <input
        type="password"
        autoComplete="new-password"
        className="form-control mb-3 text-center"
        placeholder="Contrasea"
        name="pass"
        value={pass}
        onChange={handlePassInputChange}
      />
        </div>
        <div>
        <button onClick={()=>handlePass()} className="btn btn-outline-danger btn-block">
          <i className="fas fa-trash"></i>
          <span> Eliminar</span>
        </button>
        </div>
      </Modal>
    </div>
  );
};
