import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useHistory, useLocation } from 'react-router-dom';
import { asociarCliente } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';
import moment from "moment";
import { useEffect } from 'react';
import Swal from 'sweetalert2';
import { fetchConToken } from '../../helpers/fetch';
import { Pagination } from '../ui/Pagination';
import Select from 'react-select';
import { ClienteTicketItems } from './ClienteTicketItems';
import { PDFDownloadLink } from '@react-pdf/renderer';
import { ComprobanteDetalleClientesFree } from '../ui/ComprobanteDetalleClientesFree';

const now = moment();

export const GenerarTicketFree = () => {
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const history = useHistory();
  const dispatch = useDispatch();
  const [currentPage, setCurrentPage] = useState(1);
  const [clientes, setClientes] = useState([]);
  const [eventoActual, setEventoActual] = useState({});
  const [loading, setLoading] = useState(false);
  const [planilla, setPlanilla] = useState(false);
  const [clientesPerPage, setClientesPerPage] = useState(10);
  const indexOfLastCliente = currentPage * clientesPerPage;
  const indexOfFirstCliente = indexOfLastCliente - clientesPerPage;
  const currentClientes = clientes.slice(indexOfFirstCliente,indexOfLastCliente);
  const [usuarios, setUsuarios] = useState([])
  useEffect(() => {
    const getRRPPs= async() =>{
      const resp = await fetchConToken(`auth/rppstickets`);
      const body = await resp.json();
      setUsuarios(body.rpps);
    }
    getRRPPs();
    const getEvento= async() =>{
      const resp = await fetchConToken(`eventos/detalle/${idEvento}`);
      const body = await resp.json();
      setEventoActual(body.evento);
    }
    getEvento();
  }, [idEvento])
  
  let usuarioss = [];
  for (let i = 0; i < usuarios.length; i++) {
    const usuario = {
      value:usuarios[i].uid,
      label:usuarios[i].nombre+" "+usuarios[i].apellido+"-"+usuarios[i].rol,
    }
    usuarioss.push(usuario);
  }
  const [usuario, setUsuario] = useState("");
  const [formClienteValues, handleClienteInputChange] = useForm({
      cCantidad:""
  });
  const { cCantidad} = formClienteValues;
  const tipo = { value: 0, label: `Free`};
  const evento = { value: idEvento, label: "Oporto"};
  const handleGenerar = (e) => {
      e.preventDefault();
      if (cCantidad === "") {
        return Swal.fire(
          "Error",
          "Debes de rellenar todos los campos",
          "error"
        );
      }
      const startGenerarTicketFree = async(cantidad,fecha,estado,usuario,eventoo,tipoo) => {
          let tipo = tipoo.label;
          let evento = eventoo.label;
          let eventoid = eventoo.value;
          const free = true;
          const resp = await fetchConToken(
            "clientes/freeticket",
            { cantidad,fecha,estado,usuario,evento,tipo,free,eventoid },
            "POST"
          );
          const body = await resp.json();
          if (body.ok) {
            setClientes(body.tickets);
            console.log(body.tickets)
            evento = eventoo;
            tipo = tipoo;
            for (let i = 0; i < body.tickets.length; i++) {
              dispatch(asociarCliente(body.tickets[i].uid, evento, tipo,usuario));
            }
            Swal.fire("Exito", `Se generaron ${cantidad} tickets exitosamente`, "success");
            setPlanilla(true);
          } else {
            Swal.fire("Error", body.msg, "error");
          }
      };
      startGenerarTicketFree(cCantidad, now.toDate(), true, usuario.value,evento,tipo);
  };
  const handleVolver = (e) => {
    history.goBack();
  };
  const handleUsuario = (e) => {
    setUsuario(e);
  };
  const [symbolsArr] = useState(["e", "E", "+", "-", ".",","]);
  return ( 
    <>
  <div className="col-md-6 login-form-2 mb-4">
  <h3>Generar tickets</h3>
  <hr style={{color:"white"}}/>
  <form onSubmit={handleGenerar}>
    <div className="form-group mb-4">
      <input
        type="number"
        title="Ingrese cantidad de tickets a generar"
        className="form-control mb-2 border border-primary text-center"
        placeholder="Ingrese cantidad de tickets a generar"
        name="cCantidad"
        value={cCantidad}
        autoComplete="off"
        onChange={(e)=>handleClienteInputChange(e)}
        onKeyDown={e => symbolsArr.includes(e.key) && e.preventDefault()}
      />
    </div>
    <div className="form-group mb-4">
    <label className='form-label' style={{color:"white"}}><b>Elegir usuario</b></label>
        <div className="form-control mb-2">
           <Select options={usuarioss} value={usuario} onChange={handleUsuario}/>
        </div>
    </div>
    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Generar tickets" />
    </div>
  </form>
  
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
</div>
<div className="d-flex flex-column justify-content-center align-items-center">
        <h4>Cantidad de Tickets generados</h4>
        <h3 className='border p-2 mb-3 '>{clientes.length}</h3>
        { planilla &&<PDFDownloadLink
                    document={ <ComprobanteDetalleClientesFree clientes={clientes} datos={{ evento:eventoActual.nombre, fecha:moment(eventoActual.fecha).format('DD-MM-yyyy'), usuario:usuario.value}}/>}
                    fileName={`codigos-${usuario.label}.pdf`}
                    className="text-decoration-none"
                >
                    <div className="form-group d-flex justify-content-center">
                        <button className="btn btn-primary mt-2 mb-2" type="submit"><i className="fas fa-print"></i> Imprimir codigos generados</button>
                    </div>
                </PDFDownloadLink>}
        <ClienteTicketItems clientes={currentClientes} loading={loading}/>
        <Pagination dataPerPage={clientesPerPage} totalData={clientes.length} data={clientes} setCurrentPage={setCurrentPage}/>
        <div className="form-group d-flex justify-content-center">
            <button className="btn btn-primary mb-5" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
        </div>
      </div>
</>
)
};
