import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useHistory, useLocation } from 'react-router-dom/cjs/react-router-dom.min';
import { startCargarClientePuerta } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';
import moment from "moment";
import DateTimePicker from "react-datetime-picker";
import Select from 'react-select';
import { useEffect } from 'react';
import { getPrecios} from '../../actions/eventos';

const now = moment();

export const AddClientePuerta = () => {
  const location = useLocation();
  const initialCantidad = 0;
  const [cantidad, setCantidad] = useState(initialCantidad);
  // const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const idEvento2 = location.pathname.split('/');
  const { uid } = useSelector((state) => state.auth);
  const [date, setDate] = useState(now.toDate());
  const history = useHistory();
  const dispatch = useDispatch();
  const [formClienteValues, handleClienteInputChange] = useForm({
      cDni:"",
      cNombre:"",
      cApellido:""
  });
  const { cDni, cCelular, cNombre, cApellido } = formClienteValues;
  const { precios } = useSelector((state) => state.eventos);
  let tipos = [];
  if(precios){
    tipos = [
      { value: precios[0].vip, label: `$${precios[0].vip} - VIP` },
      { value: precios[0].general, label: `$${precios[0].general} - General` },
      { value: precios[0].preferencial, label: `$${precios[0].preferencial} - Preferencial` },
    ];
  }
  const [tipo, setTipo] = useState("");
  useEffect(() => {
    dispatch(getPrecios(idEvento2[2]));
  }, []);
  const handleCargar = (e) => {
      e.preventDefault();
      if(idEvento2[2]){
        dispatch(startCargarClientePuerta(cantidad,idEvento2[2]));
      }
  };
  const handleVolver = (e) => {
    history.push('/verificar/'+idEvento2[2]);
  };
  const handleDateChange = (e) => {
    setDate(e);
  };
  const handleTipo = (e) => {
    setTipo(e);
  };
  return ( 
  <div className="col-md-6 login-form-2 mb-4">
  <h3>Cargar cliente por puerta</h3>
  <hr style={{color:"white"}}/>
  <form onSubmit={handleCargar}>
    <div className="form-group mb-2">
      <input
        type="text"
        title="Debe poner 7 digitos como minimo o 8 digitos como maximo"
        className="form-control mb-2 border border-primary text-center"
        placeholder="DNI"
        name="cDni"
        value={cDni}
        autoComplete="off"
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className="form-control mb-2 border border-primary text-center"
        placeholder="Nombre"
        name="cNombre"
        id="cNombre"
        value={cNombre}
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className="form-control mb-2 border border-primary text-center"
        placeholder="Apellido"
        name="cApellido"
        id="cApellido"
        value={cApellido}
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group">
            <DateTimePicker
              onChange={handleDateChange}
              value={date}
              className="form-control mb-3 text-center"
              format='dd-MM-yyyy HH:mm:ss'
            />
    </div>
    <hr style={{color:"white"}}/>
    <div className='text-center'>
      <label className='form-label' style={{color:"white"}}><b>Entrada</b></label>
        {precios&&<div className="form-control mb-5">
           <Select options={tipos} value={tipo} onChange={handleTipo}/>
        </div>}
    </div>
    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Cargar cliente" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
</div>);
 
};
