import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import Select from 'react-select';
// import Swal from "sweetalert2";
import { uiCloseModal } from "../../actions/ui";
import { getPrecios, setEventos, setEventosByRpp } from "../../actions/eventos";
import "../modal.css";
import { asociarCliente, startClearClienteAsoc } from "../../actions/clientes";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const AsociarClienteModal = () => {
  const dispatch = useDispatch();
  const { modalOpen } = useSelector((state) => state.ui);
  const { uid } = useSelector((state) => state.auth);
  const { clienteAsoc } = useSelector((state) => state.clientes);
  const { eventos, precios } = useSelector((state) => state.eventos);
  let eventoss = [];
  for (let i = 0; i < eventos.length; i++) {
    const evento = {
      value:eventos[i].id,
      label:eventos[i].nombre
    }
    eventoss.push(evento);
  }
  let tipos = [];
  if(precios){
    tipos = [
      { value: precios[0].vip, label: `$${precios[0].vip} - VIP` },
      { value: precios[0].general, label: `$${precios[0].general} - General` },
      { value: precios[0].preferencial, label: `$${precios[0].preferencial} - Preferencial` },
    ];
  }
  const [evento, setEvento] = useState("");
  const [tipo, setTipo] = useState("");
  useEffect(() => {
    dispatch(setEventos());
    // dispatch(setEventosByRpp(uid));
  }, [dispatch,uid]);

  const handleEvento = (e) => {
    setEvento(e);
    dispatch(getPrecios(e.value));
  };
  const handleTipo = (e) => {
    setTipo(e);
  };
  const closeModal = () => {
    dispatch(uiCloseModal());
    dispatch(startClearClienteAsoc());
  };
  const handleAsociar = () => {
    dispatch(asociarCliente(clienteAsoc, evento,tipo));
    closeModal();
    // window.location.reload();
  };
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpen}
        onRequestClose={closeModal}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      > 
        <h1 className="black">Elegir evento</h1>
        <hr />
        <div className="form-control mb-2">
           <Select options={eventoss} value={evento} onChange={handleEvento}/>
        </div>
        {precios&&<div className="form-control mb-5">
           <Select options={tipos} value={tipo} onChange={handleTipo}/>
        </div>}
        <div className="mt-5">
        <button onClick={()=>handleAsociar()} className="btn btn-outline-primary btn-block">
          <i className="fas fa-link"></i>
          <span> Asociar</span>
        </button>
        </div>
      </Modal>
    </div>
  );
};
