import React from 'react';
import { Page, Text, View, Document, Image} from '@react-pdf/renderer';

export const Comprobante = ({datos}) => {
    return (
      <Document>
        <Page
          size="A4"
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "start",
            alignItems: "center",
            backgroundColor: "white",
          }}
        >
          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "start",
              alignItems: "center",
              backgroundColor: "white",
              padding: 5,
            }}
          > 
          <Text style={{ marginTop:"35px",fontSize: "10px" }}>-------------------------------------------------------------------------</Text>
          <Text style={{ marginTop:"5px",fontSize: "10px" }}>NO COMPARTAS A NADIE ESTE DOCUMENTO</Text>
          <Text style={{ marginTop:"5px",fontSize: "10px" }}>PARA EL INGRESO SE REQUIERE DNI O ESTE DOCUMENTO</Text>
          <Text style={{ marginTop:"5px",fontSize: "10px" }}>--------------------------------------------------------------------------</Text>
            <Text style={{ marginTop:"10px",fontSize: "35px" }}>Evento</Text>
            <Text style={{ color: "#08298A",fontSize: "40px" }}>{datos.evento ? datos.evento : ""}</Text>
            <Text style={{ marginTop:"5px",fontSize: "15px" }}>Fecha</Text>
            <Text style={{ color: "#08298A",fontSize: "15px" }}>{datos.fecha ? datos.fecha : ""}</Text>
            <Text style={{ marginTop:"5px",fontSize: "25px" }}>DNI</Text>
            <Text style={{ color: "#08298A",fontSize: "25px" }}>{datos.dni ? datos.dni : ""}</Text>
            <Text style={{ marginTop:"5px",fontSize: "15px" }}>Nombre y apellido</Text>
            <Text style={{ color: "#08298A",fontSize: "15px" }}>{datos.nombre ? datos.nombre+"-"+datos.apellido : ""}</Text>
            <Text style={{ marginTop:"5px",fontSize: "15px" }}>Tipo</Text>
            <Text style={{ color: "#08298A",fontSize: "15px" }}>{datos.tipo ? datos.tipo : ""}</Text>
            <Text style={{ marginTop:"5px",fontSize: "15px" }}>Celular</Text>
            <Text style={{ color: "#08298A",fontSize: "15px" }}>{datos.celular ? datos.celular : ""}</Text>
            {datos.uri&& <Image allowDangerousPaths src={datos.uri} style={{ width:"256px", height: "256px",marginTop:"15px" }}></Image>}
          </View>
        </Page>
      </Document>
    );
};
