import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import Select from 'react-select';
import { uiCloseModall } from "../../actions/ui";
import {  eventIsFilter, eventSetActive, setEventos } from "../../actions/eventos";
import "../modal.css";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const AsociarEventoRppModal = () => {
  const dispatch = useDispatch();
  const { modalOpenn } = useSelector((state) => state.ui);
  const { eventos } = useSelector((state) => state.eventos);
  let eventoss = [];
  for (let i = 0; i < eventos.length; i++) {
    const evento = {
      value:eventos[i].id,
      label:eventos[i].nombre
    }
    eventoss.push(evento);
  }
  const [evento, setEvento] = useState("");
  useEffect(() => {
    dispatch(setEventos());
  }, [dispatch]);

  const handleEvento = (e) => {
    setEvento(e);
  };
  const closeModall = () => {
    dispatch(uiCloseModall());
    // dispatch(eventClearIsFilter());
  };
  const handleFiltrar = () => {
    dispatch(eventIsFilter());
    dispatch(eventSetActive(evento));
    closeModall();
  };
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpenn}
        onRequestClose={closeModall}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      > 
        <h1 className="black">Elegir evento a filtrar</h1>
        <hr />
        <div className="form-control mb-2">
           <Select options={eventoss} value={evento} onChange={handleEvento}/>
        </div>
        <div className="mt-5">
        <button onClick={()=>handleFiltrar()} className="btn btn-outline-primary btn-block">
          <i className="fas fa-filter"></i>
          <span>Filtrar</span>
        </button>
        </div>
      </Modal>
    </div>
  );
};
