import React from 'react';
import { useRef } from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import {  Link, useLocation } from 'react-router-dom';
import Swal from 'sweetalert2';
import { startVerificarDni, startVerificarHash } from '../../actions/clientes';
import { uiOpenModal } from '../../actions/ui';
import { fetchConToken } from '../../helpers/fetch';
import { useForm } from '../../hooks/useForm';
import { PasswordModal } from '../eventos/PasswordModal';
import { Pagination } from '../ui/Pagination';
import { ClienteVerificadosItems } from './ClientesVerificadosItems';
import {isMobile} from 'react-device-detect';
import moment from "moment";

export const VerificarClientes = () => {
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const dispatch = useDispatch();
  const [clientesPuerta, setClientesPuerta] = useState(0);
  const [formClienteValues, handleClienteInputChange,reset] = useForm({vDni:""});
  // const [formQRValues, handleQRInputChange] = useForm({qQR:""});
  // const [formHashValues, handleHashInputChange] = useForm({hash:""});
  // const { hash } = formHashValues;
  // const { qQR } = formQRValues;
  const { vDni } = formClienteValues;
  const { activeEvent } = useSelector((state) => state.eventos);
  const dni = useRef(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [clientes, setClientes] = useState([]);
  const [loading, setLoading] = useState(false);
  const [clientesPerPage] = useState(50);
  const indexOfLastCliente = currentPage * clientesPerPage;
  const indexOfFirstCliente = indexOfLastCliente - clientesPerPage;
  const currentClientes = clientes.slice(indexOfFirstCliente,indexOfLastCliente);
  //FETCHING
  useEffect(() => {
    const getClientesVerificados= async() =>{
      setLoading(true);
      const resp = await fetchConToken(`clientes/verificados/${idEvento}`);
      const body = await resp.json();
      setClientes(body.clientes);
      setLoading(false);
    }
    getClientesVerificados();
    const getClientesPuerta= async() =>{
      const resp = await fetchConToken(`eventos/puerta/${idEvento}`);
      const body = await resp.json();
      setClientesPuerta(body.cantidad);
    }
    getClientesPuerta();
  }, [idEvento]);
  const handleVerificar = (e) => {
    e.preventDefault();
    let isnum = /^\d+$/.test(vDni);
    if(!isnum){
      if (vDni.indexOf('"') >= 0) {
        const spliteado = vDni.split('"');
        console.log(spliteado);
        if(idEvento){
          dispatch(startVerificarDni(spliteado[4],idEvento));
          const getClientesVerificados= async() =>{
            setLoading(true);
            const resp = await fetchConToken(`clientes/verificados/${idEvento}`);
            const body = await resp.json();
            setClientes(body.clientes);
            setLoading(false);
          }
          getClientesVerificados();
          // const timer = setTimeout(() => {
          //   window.location.reload();
          // }, 2000);
          // return () => clearTimeout(timer);
          setTimeout(() => {
            getClientesVerificados();
          }, 1000);
          reset();
        }
      }else{
        if (vDni.indexOf('$') >= 0) {
          if(idEvento){
            let d = vDni.replaceAll('/','-');
            let a = d.replaceAll('$','@');
            dispatch(startVerificarHash(a.trim(),idEvento));
            const getClientesVerificados= async() =>{
              setLoading(true);
              const resp = await fetchConToken(`clientes/verificados/${idEvento}`);
              const body = await resp.json();
              setClientes(body.clientes);
              setLoading(false);
            }
            setTimeout(() => {
              getClientesVerificados();
            }, 1000);
            reset();
          }
        }
      }
    }else{
      if(idEvento){
        if(vDni === "111"){
          const agregarPorPuerta= async() =>{
            const resp = await fetchConToken('eventos/addpuerta',{evento:idEvento},'POST');
            const body = await resp.json();
            if(body.ok){
              window.location.reload();
              Swal.fire('Exito','Cliente por puerta cargado','success')
            }
          }
          agregarPorPuerta();
        }else{
          const verificar= async() =>{
            const resp = await fetchConToken(`clientes/dni/${vDni.trim()}/${idEvento}`);
            const body = await resp.json();
            if (resp.status === 201) {
              let dni = `${body.cliente.dni}`;
              let nombre = `${body.cliente.nombre.toUpperCase()} ${body.cliente.apellido.toUpperCase()}`;
              let horarioent =`${moment(body.cliente.entrada).format('HH:mm')}`;
              let tipo = `${body.cliente.tipo}`;
              Swal.fire({title:"INGRESO", html:'<pre> <b>DNI</b> <br/>' + dni +'<br/> <b>Nombre completo</b> <br/>'+ nombre +'<br/> <b>Horario de entrada</b> <br/>'+ horarioent + '<br/> <b>Tipo de entrada</b> <br/>'+ tipo + '</pre>', icon:"success"}).then((result)=>{
                if(result.isConfirmed){
                  const getClientesVerificados= async() =>{
                    setLoading(true);
                    const resp = await fetchConToken(`clientes/verificados/${idEvento}`);
                    const body = await resp.json();
                    setClientes(body.clientes);
                    setLoading(false);
                  }
                  setTimeout(() => {
                    getClientesVerificados();
                  }, 500);
                  // window.location.reload();
                }
              });
              // dispatch(setLoading());
            } else {
              Swal.fire("Error", body.msg, "error");
            }
        }
        verificar();
        // dispatch(startVerificarDni(vDni.trim(),idEvento));

        // const timer = setTimeout(() => {
        //   window.location.reload();
        // }, 2000);
        // return () => clearTimeout(timer);
      }
      }
    }
  };
  // const handleVerHash = (e) => {
  //   e.preventDefault();
  //   if(idEvento){
  //     dispatch(startVerificarHash(hash.trim(),vDni.trim(),idEvento));
  //   }
  // };
  // const handleAgregarPuerta = (e) => {
  //   if(e.key === 'Enter'){
  //   if(idEvento){
  //     const agregarPorPuerta= async() =>{
  //       const resp = await fetchConToken('eventos/addpuerta',{evento:idEvento,qQR},'POST');
  //       const body = await resp.json();
  //       if(body.ok){
  //         window.location.reload();
  //         Swal.fire('Exito','Cliente por puerta cargado','success')
  //       }
  //     }
  //     agregarPorPuerta();
  //   }
  // }
  // };

  const handleGoEliminar = () => {
    dispatch(uiOpenModal());
  };
  //BUSQUEDA
  const [values, setValues] = useState({cSearch:""});
  const handleSearchInputChange = ({ target }) => {
      const getClientesVerificados= async() =>{
        setLoading(true);
        const resp = await fetchConToken(`clientes/verificados/${idEvento}`);
        const body = await resp.json();
        setClientes(body.clientes);
        setLoading(false);
      }
      if(target.value===""){
        getClientesVerificados();
      }
      setValues({
      ...values,
      [target.name]: target.value,
      });
  };
  const { cSearch } = values;
  const handleBuscar = (e) => {
      e.preventDefault();
      const searchCliente = async() =>{
          const resp = await fetchConToken(`clientes/searchv/${cSearch}/${idEvento}`);
          const body = await resp.json();
          setClientes(body.clientess);
      }
      searchCliente();
  };
  return (
  <div>
    <div className='d-flex justify-content-center mb-3 mt-1'>
    {activeEvent && <span className='h1'>{activeEvent.nombre}</span>}
    </div>
    {/* <Link className='d-flex justify-content-center text-decoration-none mb-2' to={`/verificarqr/${idEvento}`}>
        <button className="btn btn-warning" title="Cargar un nuevo Cliente">
          <i className="fa-solid fa-qrcode"></i> Verificar Entrada
        </button>
    </Link> */}
    <div className='d-flex flex-column justify-content-center align-items-center'>
          <h4>Clientes que ingresaron por <b>PUERTA</b></h4>
          <h4 className='border p-2 mb-3 '>{clientesPuerta}</h4>  
    </div>
    <div className='d-flex justify-content-center align-items-center'>
      <div className='form-group text-center mb-2'>
    {/* <h2>Clientes por puerta</h2>
    <div className="form-group">
      <input
        type="text"
        className="form-control mb-2 border border-primary text-center"
        placeholder='Ingresar QR Evento'
        name="qQR"
        autoComplete="off"
        
        value={qQR || ""}
        onKeyPress={(e)=>{handleAgregarPuerta(e)}}
        onChange={handleQRInputChange}
      />
    </div> */}
        <button onClick={handleGoEliminar} className="btn btn-danger ms-1" title="Eliminar un Cliente">
          <i className="fas fa-user-minus"></i> Cliente por Puerta
        </button>
      </div>
    </div>
    <h2 className='d-flex justify-content-center'>INGRESE EL CDIGO</h2>
    <form className='mb-5' onSubmit={handleVerificar}>
    <div className="form-group">
      <input
      ref={dni}
        autoFocus
        autoComplete="off"
        type="text"
        className="form-control mb-2 border border-primary text-center"
        placeholder='CDIGO...'
        name="vDni"
        value={vDni || ""}
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group d-flex justify-content-center">
      {/* <button type="button" onClick={(e)=>{handleVerificar(e)}} className="btn btn-success btn-lg px-5" title="Verificar">
          <i className="fas fa-check"></i> Verificar
      </button> */}
      <button type="submit" className="btn btn-success btn-lg px-5" title="Verificar">
          <i className="fas fa-check"></i> Verificar
      </button>
    </div>
    </form>
    <div className="container">
      <div className="d-flex flex-column justify-content-center align-items-center">
      
      {isMobile ?<h5>Clientes que ingresaron con <b>ANTICIPADA</b></h5> : <h4>Clientes que ingresaron con <b>ANTICIPADA</b></h4>}
      <h4 className='border p-2 mb-3 '>{clientes.length}</h4>
      <div className='w-50'>
        <form className='form-group d-flex justify-content-center align-items-center mb-2' onSubmit={handleBuscar}>
            <input className='form-control me-2 text-center' title='Busqueda por apellido' placeholder='Ingrese el apellido o DNI completo' type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
            <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
        </form>
      </div>
      <ClienteVerificadosItems clientes={currentClientes} loading={loading}/>
      <PasswordModal />
      <Pagination dataPerPage={clientesPerPage} totalData={clientes.length} data={clientes} setCurrentPage={setCurrentPage}/>
      </div>
    </div>
  </div>
  );
};
