import React from 'react';
import { Page, Text, View, Document,StyleSheet} from '@react-pdf/renderer';

const styles = StyleSheet.create({
  table: {
    width: '100%'
  },
  row: {
    display: 'flex',
    flexDirection: 'row',
    borderTop: '1px solid #EEE',
    paddingTop: 8,
    paddingBottom: 8
  },
  header: {
    borderTop: 'none',
  },
  bold: {
    fontWeight: 'bold',
  },
  // So Declarative and unDRY 
  row1: {
    width: '20%',
  },
  row2: {
    width: '20%',
  },
  row3: {
    width: '20%',
  },
  row4: {
    width: '20%',
  },
  row5: {
    width: '20%',
  },
})

export const ComprobanteDetalleClientes = ({datos,clientes}) => {  
    return (
      <Document>
        <Page
          size="A4"
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "start",
            alignItems: "center",
            backgroundColor: "white", 
            padding: 25
          }}
        >
          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "start",
              alignItems: "center",
              backgroundColor: "white",
              padding: 5,
            }}
          > 
            <Text style={{ marginTop:"35px",fontSize: "30px" }}>Evento</Text>
            <Text style={{ color: "#08298A",fontSize: "30px",marginBottom:"10px" }}>{datos.evento ? datos.evento : ""}</Text>
            <Text style={{ fontSize: "20px" }}>Fecha</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"5px" }}>{datos.fecha ? datos.fecha : ""}</Text>
            <Text style={{ fontSize: "20px", marginBottom:"5px" }}>Planilla</Text>
            <Text style={{ fontSize: "15px" }}>Cantidad de Clientes</Text>
            <Text style={{ color: "#08298A",fontSize: "15px",marginBottom:"10px" }}>{datos.cantCli ? datos.cantCli : ""}</Text>
            <View style={styles.table}>
              <View style={[styles.row, styles.bold, styles.header]} fixed>
                <Text style={{width: '20%', border:"1px solid black"}}>DNI</Text>
                <Text style={{width: '20%', border:"1px solid black"}}>NOMBRE</Text>
                <Text style={{width: '20%', border:"1px solid black"}}>APELLIDO</Text>
                <Text style={{width: '20%', border:"1px solid black"}}>USUARIO</Text>
                <Text style={{width: '20%', border:"1px solid black"}}>ENTRADA</Text>
              </View>
              {clientes.map(cliente => (
                <View key={cliente.dni} style={styles.row} wrap={false}>
                  <Text style={{width: '20%'}} >{cliente.dni}</Text>
                  <Text style={{width: '20%'}} >{cliente.nombre}</Text>
                  <Text style={{width: '20%'}} >{cliente.apellido}</Text>
                  <Text style={{width: '20%',fontSize: "15px",flexWrap:"wrap"}}>{cliente.usuario.nombre} {cliente.usuario.apellido}</Text>
                  <Text style={{width: '20%',fontSize: "15px",flexWrap:"wrap"}}>{cliente.tipo}</Text>
                </View>
              ))}
            </View>
          </View>
        </Page>
      </Document>
    );
};
