import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { startCheckingWsp } from '../../actions/ui';
import { fetchConToken } from '../../helpers/fetch';

export const AdminScreen = () => {
  const { estadoWsp } = useSelector((state) => state.ui);
  const handleConectarWsp = () => {
    const getQr = async() =>{
      const resp = await fetchConToken("wsp/auth/getqr");
      const body = await resp.json();
      document.write(body.pagina);
    }
    getQr();
  };
  const dispatch = useDispatch();
  useEffect(() => {
    setTimeout(function(){
      dispatch(startCheckingWsp());
    }, 1000);
    setTimeout(function(){
      dispatch(startCheckingWsp());
    }, 5000);
  }, [dispatch]);
//   const handleclientefalse = () => {
//     const ticketfalse = async() =>{
//         const respu = await fetchConToken(`clientes/agregarticketfalse`);
//         const body = await respu.json();
//     }
//     ticketfalse();
// };
  return (
    <div className='container text-center'>
        <h2 className='mb-5 mt-2' style={{color:"black"}}>Administrar</h2>
        <div className='d-flex flex-row justify-content-evenly align-items-center'>
        {/* <button className="btn btn-primary" onClick={()=>{handleclientefalse()}}><i className="fas fa-broom"></i> Ticketfalse</button> */}
            <Link to="/controlrpp">
                <button className="btn btn-primary btn-lg" title="Cargar un nuevo RPP">
                <i className="fas fa-user"></i> Controlar RRPP
                </button>
            </Link>
            <Link to="/controlevento">
                <button className="btn btn-primary btn-lg" title="Cargar un nuevo RPP">
                <i className="fas fa-calendar-alt"></i> Controlar Eventos
                </button>
            </Link>
        </div>
        <div className='d-flex flex-column justify-content-center align-items-center mt-5'>
        <div className='d-flex justify-content-center mb-2'><span className='bg-light rounded'>Estado <i style={{"fontSize":"1.1rem","color":"green","marginLeft":"-8px","marginRight":"-5px"}} className="py-3 px-2 fab fa-whatsapp fa-xs position-sticky"></i>: <b className={estadoWsp==="Conectado"?'text-success':'text-danger'}>{estadoWsp}</b></span></div>
        {estadoWsp === "Desconectado" && 
          <div className='mb-5'>
            <div className="form-group d-flex justify-content-center align-items-center">
              <button className="btn btn-success mt-2 d-flex justify-content-center align-items-center" onClick={handleConectarWsp}><i style={{"fontSize":"1.5rem"}} className="py-0 px-2 fab fa-whatsapp fa-xs position-sticky"></i> Conectar WSP</button>
            </div> 
          </div>
        }
        </div>
    </div>
  );
};
