import React, { useEffect, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { fetchConToken } from '../../helpers/fetch';
import { ClienteItems } from './ClienteItems';
import { Pagination } from '../ui/Pagination';
import { useSelector } from 'react-redux';
import { AsociarClienteModal } from './AsociarClienteModal';
import { useForm } from '../../hooks/useForm';
import { useDispatch } from 'react-redux';
import { searchCliente } from '../../actions/clientes';
import {isMobile} from 'react-device-detect';
import { AsociarEventoModal } from './AsociarEventoModal';
import { uiOpenModall } from '../../actions/ui';
import { eventClearIsFilter } from '../../actions/eventos';
import { useLocation } from 'react-router-dom/cjs/react-router-dom.min';
import { ClienteItemsFreeList } from './ClienteItemsFreeList';

export const ClienteListaFreeScreen = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const [currentPage, setCurrentPage] = useState(1);
  const [clientes, setClientes] = useState([]);
  const [loading, setLoading] = useState(false);
  const [clientesPerPage, setClientesPerPage] = useState(10);
  const indexOfLastCliente = currentPage * clientesPerPage;
  const indexOfFirstCliente = indexOfLastCliente - clientesPerPage;
  const currentClientes = clientes.slice(indexOfFirstCliente,indexOfLastCliente);
  //FETCHING
  useEffect(() => {
    const getClientesFreeByEvento= async() =>{
      const resp = await fetchConToken(`clientes/getfreeevento/${idEvento}`);
      const body = await resp.json();
      setClientes(body.clientes);
    }
    getClientesFreeByEvento();
  }, [idEvento]);
  const handleVolver = (e) => {
    history.push(`/detalle/${idEvento}`);
  };
  return ( 
    <div className="container">
      {isMobile
      ?
      <div className="d-flex flex-column justify-content-center align-items-center">
        <Link className='ms-1 d-flex justify-content-end text-decoration-none' to={`/listaticketsfree/${idEvento}`}>
          <button className="btn btn-success" title="Lista de tickets free">
            <i className="fas fa-user-plus"></i> Lista de tickets FREE
          </button>
        </Link>
      </div>
      :
      <div className="d-flex flex-row justify-content-center  align-items-center mb-2">
        <Link className='ms-1 d-flex justify-content-end text-decoration-none' to={`/listaticketsfree/${idEvento}`}>
          <button className="btn btn-success" title="Lista de tickets free">
            <i className="fas fa-user-plus"></i> Lista de tickets FREE
          </button>
        </Link>
      </div>
      }
      
      <div className="d-flex flex-column justify-content-center align-items-center">
        <h4>Cantidad de Clientes FREE</h4>
        <h3 className='border p-2 mb-3 '>{clientes.length}</h3>
        <ClienteItemsFreeList clientes={currentClientes} loading={loading}/>
        <Pagination dataPerPage={clientesPerPage} totalData={clientes.length} data={clientes} setCurrentPage={setCurrentPage}/>
        <div className="form-group d-flex justify-content-center">
            <button className="btn btn-primary mb-5" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
        </div>
      </div>
    </div>
  );
};
