import React from 'react';
import { useDispatch } from 'react-redux';
import { startAsociar, startEliminarRRPP, startSetRrppActive } from '../../actions/auth';
import { uiOpenModal } from '../../actions/ui';
import {isMobile} from 'react-device-detect';
import { useHistory } from 'react-router-dom';

export const RppItems = ({rpps,loading}) => {
  const dispatch = useDispatch();
  const history = useHistory();
  if(loading){
    return <h2>Cargando...</h2>
  }
  // const handleGoAsociar = (uid) => {
  //     dispatch(startAsociar(uid));
  //     dispatch(uiOpenModal());
  // };
  const handleGoEditar = (rid) => {
    dispatch(startSetRrppActive(rid));
    setTimeout(()=>{
      history.push(`/editarrrpp/${rid}`);
    },500);
  };
  const handleGoEliminar = (uid) => {
    dispatch(startEliminarRRPP(uid));
    history.push('/controlrpp');
  };
  return(
  <>
    {rpps && <table className={isMobile ? "d-block overflow-scroll table table-bordered table-striped":"table table-bordered table-striped"}>
  <thead className="bg-dark text-light">
    <tr>
      <th className="text-center" scope="col">DNI</th>
      <th className="text-center" scope="col">Nombre</th>
      <th className="text-center" scope="col">Apellido</th>
      <th className="text-center" scope="col">Estado</th>
      <th className="text-center" scope="col">Email</th>
      <th className="text-center" scope="col">Rol</th>
      <th className="text-center" scope="col">Deuda</th>
      <th className="text-center" scope="col">Total Entradas</th>
      <th className="text-center" scope="col">Total Favor</th>
      <th className="text-center" scope="col">Editar</th>
      <th className="text-center" scope="col">Eliminar</th>
      {/* <th className="text-center" scope="col">Asociar Evento</th> */}
    </tr>
  </thead>
  <tbody>
  {rpps.map(item => {
      return (
        <tr key={item.uid}>
          <td className="text-center">{ item.dni }</td>
          <td className="text-center">{ item.nombre }</td>
          <td className="text-center">{ item.apellido }</td>
          <td className="text-center">{ item.estado ? "Activo" : "Inactivo"  }</td>
          <td className="text-center">{ item.email }</td>
          <td className="text-center">{ item.rol }</td>
          <td className="text-center">{ item.deuda?item.deuda:"-" }</td>
          {/* <td className="text-center">{ item.totalEntradas?item.totalEntradas:"-" }</td> */}
          {item.eventos.map(evento => {
              return  <td key={item.uid} className="text-center">{ evento.totalEntradas?evento.totalEntradas:"-" }</td>
          })}
          {item.eventos.map(evento => {
              return  <td key={item.uid} className="text-center">{ evento.totalEntradas-evento.deuda }</td>
          })}
          <td className="text-center"><button className='btn btn-warning' onClick={()=>handleGoEditar(item.uid)}>Editar</button></td>
          <td className="text-center"><button className='btn btn-danger' onClick={()=>handleGoEliminar(item.uid)}><i className="fas fa-trash-alt"></i></button></td>
          {/* <td className="text-center"><button className='btn btn-primary' onClick={()=>handleGoAsociar(item.uid)}>Asociar</button></td> */}
        </tr>
      );
    })}
  </tbody>
</table>}
</> );
};
