import React, { useEffect } from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import { Switch, Route,Redirect } from "react-router-dom";
import { HomeScreen } from "../components/eventos/HomeScreen";
import { AddCliente } from "../components/clientes/AddCliente";
import { AddEvento } from "../components/eventos/AddEvento";
import { AddRpp } from "../components/rpps/AddRpp";
import { Navbar } from "../components/ui/Navbar";
import { NotFoundScreen } from "../components/ui/NotFoundScreen";
import { VerificarClientes } from "../components/clientes/VerificarClientes";
import { EventoElegir } from "../components/eventos/EventoElegir";
import { EventoScreen } from "../components/eventos/EventoScreen";
import { RppScreen } from "../components/rpps/RppScreen";
import { EventoDetalle } from "../components/eventos/EventoDetalle";
import { EventoEditar } from "../components/eventos/EventoEditar";
import { AddRppOperativo } from "../components/rpps/AddRppOperativo";
import { AddClienteGeneral } from "../components/clientes/AddClienteGeneral";
import { ClienteGeneralScreen } from "../components/clientes/ClienteGeneralScreen ";
import { AddClientePuerta } from "../components/clientes/AddClientePuerta";
import { VerificarQRClientes } from "../components/clientes/VerificarQRClientes";
import { ClienteEditar } from "../components/clientes/ClienteEditar";
import { AddClienteFree } from "../components/clientes/AddClienteFree";
import { RrppEditar } from "../components/rpps/RrppEditar";
import { ClienteListaFreeScreen } from "../components/clientes/ClienteListaFreeScreen";
import { AddClienteFreeList } from "../components/clientes/AddClienteFreeList";
import { ClienteTicketsGenerados } from "../components/clientes/ClienteTicketsGenerados";
import { GenerarTicketFree } from "../components/clientes/GenerarTicketFree";
export const DashboardRoutes = () => {
  const { rol } = useSelector((state) => state.auth);
  const [isAdmin, setisAdmin] = useState(false);
  const [isRRPP, setisRRPP] = useState(false);
  useEffect(() => {
    if(rol === 'ADMIN'){
      setisAdmin(true);
    }
    if(rol === 'RRPP'){
      setisRRPP(true);
    }
  }, [setisAdmin,setisRRPP,rol]);
  
  return (
    <>
      <Navbar/>
      <div>
        <div className="d-flex justify-content-center align-items-center flex-column">
        <Switch>
          <Route exact path="/" component={HomeScreen} />
          {isAdmin ? <Route path={`/addrpp`} component={AddRpp}/> : <Route path={`/addcliente`} component={AddCliente} />}
          {isAdmin && <Route path={`/addevento`} component={AddEvento}/>}
          <Route path={`/addclientefree`} component={AddClienteFree}/>
          {isAdmin && <Route exact path={`/verificar`} component={EventoElegir}/>}
          {isAdmin && <Route exact path={`/controlrpp`} component={RppScreen}/>}
          {isAdmin && <Route exact path={`/controlevento`} component={EventoScreen}/>}
          {isAdmin && <Route exact path={`/verificar/:id`} component={VerificarClientes}/>}
          {isAdmin && <Route exact path={`/verificarqr/:id`} component={VerificarQRClientes}/>}
          {isAdmin && <Route path={`/verificar/:id/addcliente`} component={AddClientePuerta}/>}
          {isAdmin && <Route path={`/editar/:idevento`} component={EventoEditar}/>}
          {isAdmin && <Route path={`/editarrrpp/:idrrpp`} component={RrppEditar}/>}
          {isAdmin && <Route path={`/listafree/:idevento`} component={ClienteListaFreeScreen}/>}
          {isAdmin && <Route path={`/listaticketsfree/:idevento`} component={ClienteTicketsGenerados}/>}
          {isAdmin && <Route path={`/generartickets/:idevento`} component={GenerarTicketFree}/>}
          <Route path={`/editarcliente/:idcliente`} component={ClienteEditar}/>
          {isAdmin && <Route path={`/detalle/:idevento`} component={EventoDetalle}/>}
          <Route path={`/addclientegeneral`} component={AddClienteGeneral}/>
          {/* {isGeneral && <Route path={`/addrppoperativo`} component={AddRppOperativo}/>} */}
          <Route exact path={`/controlcliente`} component={ClienteGeneralScreen}/>
          <Route path="*" component={NotFoundScreen} />
          <Redirect to="/"/>
        </Switch>
        </div>
      </div>
    </>
  );
};
