import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { eventSetActive, startEliminarEvento } from '../../actions/eventos';
import { fetchConToken } from '../../helpers/fetch';

export const EventoScreen = () => {
    const history = useHistory();
    const [eventos, setEventos] = useState([]);
    const dispatch = useDispatch();
    useEffect(() => {
        const getEventos = async() =>{
            const resp = await fetchConToken(`eventos`);
            const body = await resp.json();
            setEventos(body.eventos);
        }
        getEventos();
    }, []);

    const handleEvento = (e,evento) => {
        if(e.target.localName !== "button" && e.target.localName !== "i"){
            dispatch(eventSetActive(evento));
            history.push(`/detalle/${evento.id}`);
        }
    };
    const handleVolver = (e) => {
        history.push('/');
    };
    const handleEliminar = (evento) => {
        dispatch(startEliminarEvento(evento));
        const timer = setTimeout(() => {
            window.location.reload();
          }, 500);
        return () => clearTimeout(timer);
    };
  return (
  <div className='container-fluid d-flex justify-content-center align-items-center flex-column'>
      <Link className="mb-3" to="/addevento">
        <button className="btn btn-success" title="Cargar un nuevo RPP">
          <i className="fas fa-plus-circle"></i> Evento
        </button>
      </Link>
      <div>
        <div className='row m-auto'>
        {eventos.map(evento => {
        return ( 
                <div className='col d-flex justify-content-center align-items-center mb-2' role="button" key={evento.id} >
                    <div className="card d-flex justify-content-center align-items-center" style={{width: "12rem"}} onClick={(e)=>handleEvento(e,evento)}>
                    <button className="btn btn-danger btn-sm align-self-baseline" onClick={()=>handleEliminar(evento)}><i className="fas fa-times fa-xs"></i></button>
                        <div className="card-body">
                            <h5 className="card-title"><b>{evento.nombre}</b></h5>
                            <p className="card-text">{evento.ubicacion}</p>
                        </div>
                    </div>
                </div>
        )
        })}
        </div>
        <div className="d-flex justify-content-center align-items-center">
            <button className="btn btn-primary mt-2" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
        </div>
    </div>
  </div>
  );
};
