import React from 'react';
import { useRef } from 'react';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import {  useHistory, useLocation } from 'react-router-dom';
import { useForm } from '../../hooks/useForm';
import { startVerificarHash } from '../../actions/clientes';

export const VerificarQRClientes = () => {
  const history = useHistory();
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const dispatch = useDispatch();
  const [formClienteValues, handleClienteInputChange] = useForm({vDni:"",hash:""});
  // const [formHashValues, handleHashInputChange] = useForm({hash:""});
  // const { hash } = formHashValues;
  const { vDni, hash } = formClienteValues;
  const { activeEvent } = useSelector((state) => state.eventos);
  // const dni = useRef(null);

  const handleVerHash = (e) => {
    e.preventDefault();
    if(idEvento){
      let d = hash.replaceAll('/','-');
      let a = d.replaceAll('$','@');
      dispatch(startVerificarHash(a.trim(),idEvento));
    }
  };
  const handleVolver = (e) => {
    history.push(`/verificar/${idEvento}`);
  };

  return (
  <div>
    <div className='d-flex justify-content-center mb-3 mt-1'>
    {activeEvent && <span className='h1'>{activeEvent.nombre}</span>}
    </div>
    <h2 className='d-flex justify-content-center'>Verificar QR entrada</h2>
    <form className='mb-5' onSubmit={handleVerHash}>
    <div className="form-group">
    {/* <input
      ref={dni}
        autoFocus
        autoComplete="off"
        type="text"
        className="form-control mb-2 border border-primary text-center"
        placeholder='Ingrese DNI...'
        name="vDni"
        value={vDni}
        onChange={handleClienteInputChange}
      /> */}
      <input
        autoComplete="off"
        type="password"
        title="Verificar el QR"
        className="form-control mb-2 border border-primary text-center"
        placeholder='Ingrese QR...'
        name="hash"
        value={hash}
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btn btn-warning btn-lg px-5" value="Verificar QR" />
    </div>
    </form>
    <div className="form-group d-flex justify-content-center mb-2">
        <button className="btn btn-primary" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
    </div>
  </div>
  );
};
