import React from 'react';
import moment from 'moment';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation,useHistory } from 'react-router-dom';
import { startDesasociarCliente, startSetActive } from '../../actions/clientes';
import {isMobile} from 'react-device-detect';

export const ClienteItemsDetalle = ({clientes,loading}) => {
  const dispatch = useDispatch();
  const { uid } = useSelector((state) => state.auth);
  const location = useLocation();
  const history = useHistory();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
    if(loading){
        return <h2>Cargando...</h2>
    }
    const handleGoDesasociar = (cid) => {
        dispatch(startDesasociarCliente(cid,idEvento,uid));
        setTimeout(()=>{
          history.push(`/detalle/${idEvento}`);
        },500);
    };
    const handleGoEditar = (cid) => {
      dispatch(startSetActive(cid));
      setTimeout(()=>{
        history.push(`/editarcliente/${cid}`);
      },500);
    };
  return (
    <>
      {clientes && <table className={isMobile ? "d-block overflow-scroll table table-sm table-bordered table-striped":"table table-sm table-bordered table-striped"}>
    <thead className="bg-dark text-light">
      <tr>
        <th className="text-center align-middle" scope="col">DNI</th>
        <th className="text-center align-middle" scope="col">Nombre</th>
        <th className="text-center align-middle" scope="col">Apellido</th>
        <th className="text-center align-middle" scope="col">Fecha</th>
        <th className="text-center align-middle" scope="col">Celular</th>
        <th className="text-center align-middle" scope="col">Entrada</th>
        <th className="text-center align-middle" scope="col">Tipo entrada</th>
        <th className="text-center align-middle" scope="col">Usuario</th>
        <th className="text-center align-middle" scope="col">Desasociar</th>
        <th className="text-center align-middle" scope="col">Editar</th>
      </tr>
    </thead>
    <tbody>
    {clientes.map(item => {
        return (
          <tr key={item.uid}>
            <td className="text-center">{ item.dni }</td>
            <td className="text-center">{ item.nombre }</td>
            <td className="text-center">{ item.apellido }</td>
            <td className="text-center">{ moment(item.fecha).format("DD/MM/YYYY HH:mm") }</td>
            <td className="text-center">{ item.celular }</td>
            {item.entradas.map(entrada => {
                return entrada.evento===idEvento &&( <td key={item.uid} className="text-center">$ { entrada.importe }</td>)
            })}
            {item.entradas.map(entrada => {
                return entrada.evento===idEvento &&( <td key={item.uid} className="text-center">{ entrada.tipo }</td>)
            })}
            <td className="text-center">{ item.usuario.nombre+" "+item.usuario.apellido }</td>
            <td className="text-center"><button className='btn btn-danger' onClick={()=>handleGoDesasociar(item.uid)}><i className="fas fa-trash-alt"></i></button></td>
            <td className="text-center"><button className='btn btn-warning' onClick={()=>handleGoEditar(item.uid)}>Editar</button></td>
          </tr>
        );
      })}
    </tbody>
  </table>}
  </> );
};
