import { fetchConToken } from "../helpers/fetch";
import { types } from "../types/types";

export const uiOpenModal=()=>({
    type:types.uiOpenModal
})
export const uiCloseModal=()=>({
    type:types.uiCloseModal
})

export const uiOpenModall=()=>({
  type:types.uiOpenModall
})
export const uiCloseModall=()=>({
  type:types.uiCloseModall
})

export const startCheckingWsp = () => {
    return async (dispatch) => {
      try {
        const resp = await fetchConToken("wsp/auth/checkauth");
        const body = await resp.json();
        dispatch(getEstadoWsp(body.estado))
      } catch (error) {
        console.log(error);
      }
    };
  };
  const getEstadoWsp = (estado) => ({
    type: types.getEstadoWsp,
    payload: estado,
  });